<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox registerBox">
			<div>
				<div class="registerTop">
					<img v-if="theme == 'sun'" v-lazy="require('@/assets/logoSun.png')">
					<img v-else v-lazy="require('@/assets/logo.png')">
					<span>{{$t('WelcomeToDeFiEra')}}</span>
				</div>
				<div class="content">		
					<div>
						<span>{{$t('EnterNickname')}}</span>
						<input type="text" v-model="name" :placeholder="$t('EnterYourNickname')" maxlength="6">
					</div>
					<div>
						<span>{{$t('BindInviter')}}</span>
						<input type="text" v-model="fromCode" :placeholder="$t('inviterText01')" @input="fromCode=fromCode.replace(/[\W]/g,'')">
					</div>
					<div>
						<span>{{$t('inviterText02')}}</span>
						<input type="text" v-model="code" :placeholder="$t('inviterText03')" @input="code=code.replace(/[\W]/g,'')">
					</div>
					<Button color="linear-gradient(44deg, #21C17A, #21C1A8)" @click="bind" v-ButClickLimit>{{$t('Commit')}}</Button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { navBar, Button },
		data() {
			return {
				title: this.$t('registered'),
				code: '',
				name: '',
				fromCode: '',
				theme: localStorage.getItem('theme')
			}
		},
		methods: {
			async bind() {
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/
				
				if (this.fromCode.length !== 6) {
					return Toast.fail(this.$t('warningText14'))
				} else if (!reg.test(this.name) || this.name.length > 6) {
					return Toast.fail(this.$t('warningText13'))
				} else if (this.code.length !== 6) {
					return Toast.fail(this.$t('warningText15'))
				}
				
				let code = this.$web3.utils.stringToHex(this.code.toUpperCase()),
					name = this.$web3.utils.stringToHex(this.name),
				    fromCode = this.$web3.utils.stringToHex(this.fromCode.toUpperCase()),
				    thisAddress = await this.$contract.getData('relationship').methods.ShortCodeToAddress(code).call(),
				    fromAddress = await this.$contract.getData('relationship').methods.ShortCodeToAddress(fromCode).call()
				
				if (!parseInt(fromAddress, 16)) {
				    return Toast.fail(this.$t('warningText16'))
				} else if (parseInt(thisAddress, 16)) {
				    return Toast.fail(this.$t('warningText17'))
				}
				
				this.$contract.getData('relationship').methods.AddRelationEx(fromAddress, code, name)
				.call({ from: this.$address }, (err, res) => {
					if (!err) {
						if (res === '0') {
							this.$contract.getData('relationshipI').methods.AddRelationEx(fromAddress, code, name).send({
								from : this.$address
							}).on('transactionHash' , txHash =>{
								console.log(txHash)
							}).on('error' , err=>{
								console.log(err)
							}).on('receipt' , result=>{
								Toast.success(this.$t('warningText18'))
								xwClearInvitationCode.postMessage('1')
								let timeOut = setTimeout(() => {
									clearTimeout(timeOut)
									this.App_Pop()
								}, 2000)
							})
						} else {
							Toast.fail(this.$t('warningText19'))
						}
					}
				})
			},
			GetQueryString(name) {
				return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
			}
		},
		async mounted() {
			// this.fromCode = this.GetQueryString('code')
			if(localStorage.getItem('invitationCode') && localStorage.getItem('invitationCode').length == 6){				
				this.fromCode = localStorage.getItem('invitationCode')
				var fromAddress = await this.$contract.getData('relationship').methods.ShortCodeToAddress(this.$web3.utils.stringToHex(this.fromCode.toUpperCase())).call()
				if(!parseInt(fromAddress, 16)){return this.fromCode = ''}
			}
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

