<template>
	<div class="box">		
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox">
			<div class="orderSelect">
				<div :class="{active:active == 0}" @click="active = 0">{{$t('All')}}({{orderTotalNumber}})</div>
				<div :class="{active:active == 1}" @click="active = 1">{{$t('Provide')}}</div>
				<div :class="{active:active == 2}" @click="active = 2">{{$t('Get')}}</div>
				<div :class="{active:active == 3}" @click="active = 3">{{$t('Share')}}</div>
			</div>
			<div class="orderListBox">
				<div v-for="(item,index) in ProvideHelpList" :key="index" v-if="active == 0" @click="App_Push('/orderDetails','order',item.order)">
					<div class="MyHelpListBoxContentRight">
						<div class="MyHelpListBoxContentRightRate">
							<span class="green" v-if="item.type == 3 && item.state == '6'">{{ $t('ReferralIncome') }}</span>
							<span class="gray" v-else-if="item.type == 3 && item.state == '7'">{{ $t('ReferralIncome') }}</span>
							<span class="green" v-else-if="item.state == '2' || (item.state == '5' && !item.CurrentProfitInfo)">{{$t('ProvideHelp')}}</span>
							<span class="yellow" v-else-if="item.state == '5' && item.CurrentProfitInfo">{{$t('IncomeCollection')}}</span>
							<span class="green" v-else-if="item.state == '4'">{{$t('Default')}}</span>
							<span class="gray" v-else>{{$t('GetHelp01')}}</span>
							<div v-if="item.OrderCountdownStarts">
								<span>{{$t('PerformCountdown')}}</span>
								<div>
									<div>{{item.timesDown.substr(0,1)}}</div>
									<div>{{item.timesDown.substr(1,1)}}</div>
									<p>:</p>
									<div>{{item.timesDown.substr(3,1)}}</div>
									<div>{{item.timesDown.substr(4,1)}}</div>
									<p>:</p>
									<div>{{item.timesDown.substr(6,1)}}</div>
									<div>{{item.timesDown.substr(7,1)}}</div>
								</div>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightContent">
							<div>									
								<div class="MyHelpListBoxContentRightCircle">
									<icon name="success" class="active"></icon>
									<div :style="'width:'+(item.isComplete ? 100 : 50)+'%'" :class="{yellowBackground:(item.state == '5' && item.CurrentProfitInfo),grayBackground:item.state == '7'}"></div>
									<div :class="{yellowBorder:(item.state == '5' && item.CurrentProfitInfo),grayBorder:item.state == '7'}"></div>
								</div>
								<div class="MyHelpListBoxContentRightTotal" :class="{yellow:(item.state == '5' && item.CurrentProfitInfo),gray:item.state == '7'}">
									{{item.totalAmount}}
									<span>USDT</span>
								</div>
							</div>
							<div class="MyHelpListBoxContentRightToFrom colorA1BDE1">
								<span>{{name}}</span>
								<img :class="{rotate180:(item.state == '5' && item.CurrentProfitInfo) || item.state == '6' || item.state == '7'}" src="../../../assets/send.png" />
								<span>Contract</span>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightButton">
							<div>
								<span>{{$toTime(item.cretaTime,'Y/M/D H:i')}}</span>
							</div>
							<div class="green">{{$t('Details')}}</div>
						</div>
					</div>
				</div>
				<div v-for="(item,index) in ProvideHelpList" :key="index" v-if="active == item.type" @click="App_Push('/orderDetails','order',item.order)">
					<div class="MyHelpListBoxContentRight">
						<div class="MyHelpListBoxContentRightRate">
							<span class="green" v-if="item.type == 3 && item.state == '6'">{{ $t('ReferralIncome') }}</span>
							<span class="gray" v-else-if="item.type == 3 && item.state == '7'">{{ $t('ReferralIncome') }}</span>
							<span class="green" v-else-if="item.state == '2' || (item.state == '5' && !item.CurrentProfitInfo)">{{$t('ProvideHelp')}}</span>
							<span class="yellow" v-else-if="item.state == '5' && item.CurrentProfitInfo">{{$t('IncomeCollection')}}</span>
							<span class="green" v-else-if="item.state == '4'">{{$t('Default')}}</span>
							<span class="gray" v-else>{{$t('GetHelp01')}}</span>
							<div v-if="item.OrderCountdownStarts">
								<span>{{$t('PerformCountdown')}}</span>
								<div>
									<div>{{item.timesDown.substr(0,1)}}</div>
									<div>{{item.timesDown.substr(1,1)}}</div>
									<p>:</p>
									<div>{{item.timesDown.substr(3,1)}}</div>
									<div>{{item.timesDown.substr(4,1)}}</div>
									<p>:</p>
									<div>{{item.timesDown.substr(6,1)}}</div>
									<div>{{item.timesDown.substr(7,1)}}</div>
								</div>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightContent">
							<div>									
								<div class="MyHelpListBoxContentRightCircle">
									<icon name="success" class="active"></icon>
									<div :style="'width:'+(item.isComplete ? 100 : 50)+'%'" :class="{yellowBackground:(item.state == '5' && item.CurrentProfitInfo),grayBackground:item.state == '7'}"></div>
									<div :class="{yellowBorder:(item.state == '5' && item.CurrentProfitInfo),grayBorder:item.state == '7'}"></div>
								</div>
								<div class="MyHelpListBoxContentRightTotal" :class="{yellow:(item.state == '5' && item.CurrentProfitInfo == '1'),gray:item.state == '7'}">
									{{item.totalAmount}}
									<span>USDT</span>
								</div>
							</div>
							<div class="MyHelpListBoxContentRightToFrom colorA1BDE1">
								<span>{{name}}</span>
								<img :class="{rotate180:(item.state == '5' && item.CurrentProfitInfo == '1') || item.state == '6' || item.state == '7'}" src="../../../assets/send.png" />
								<span>Contract</span>
							</div>
						</div>
						<div class="MyHelpListBoxContentRightButton">
							<div>
								<span>{{$toTime(item.cretaTime,'Y/M/D H:i')}}</span>
							</div>
							<div class="green">{{$t('Details')}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { Rate , Popup , Toast , Icon , Overlay } from 'vant';
	import navBar from '@/components/nav-bar'
	import Order from "../../../static/Order.json"
	export default {
		components: { navBar , Icon },
		data(){
			return {
				title: this.$t('HistoricalOrder'),
				active: 0,
				ProvideHelpList: [],
				orderTotalNumber: 0,
			}
		},
		methods:{
			async init(){
				// 获取名字
				this.name =  this.$web3.utils.hexToString(await this.$contract.getData('relationship').methods.AddressToNickName(this.$address).call())

				// try{
					// 循环出所有type为0的订单
					this.getOrderTotal(0).then(result=>{
						this.orderTotal = result
						for(var i = this.orderTotal;i >= 0;i-=10){
							this.cretaOrder(0,i)
						}
					});
					
					// 循环出所有type为2的订单(推荐订单)
					this.getOrderTotal(2).then(result=>{
						this.incomeOrderTotal = result 
						for(var k = this.incomeOrderTotal;k >= 0;k-=10){
							this.cretaOrder(2,k)
						}
					});

				// } catch(e) {}	
				
				// 最新块的时间
				var currentTime = await this.$currentTime()
				this.times = setInterval(()=>{
					currentTime++;
					this.ProvideHelpList.forEach((item,index)=>{
						if(item.state == '2'){
							if(currentTime == 0 || currentTime > item.OnCountDownEnd){
								this.$set(this.ProvideHelpList[index],'timesDown','00:00:00:00')
								clearInterval(this.times)
								return 
							}else{
								var timesDown = this.timeDown(item.OnCountDownEnd,currentTime);
								this.$set(this.ProvideHelpList[index],'timesDown',timesDown)
							}
						}
					})
				},1000)
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
			async getOrderTotal(type){ //获取订单最高偏移量
				// 订单总量
				var orderTotal = (await this.$contract.getData('controller').methods.QueryOrders(this.$address,type,255,0,10).call()).total;
				this.orderTotalNumber += Number(orderTotal)
				if(Number(orderTotal) <= 10){
					var maxOffset = 0
				}else{
					var maxOffset = Number(orderTotal.substr(0,1)) * 10
				}
				// console.log(orderTotal,maxOffset)
				return maxOffset
			},
			async cretaOrder(stateType,offset){
				// 订单
				var QueryOrders = await this.$contract.getData('controller').methods.QueryOrders(this.$address,stateType,255,offset,10).call()
				// console.log(QueryOrders)
				for(var key = (Number(QueryOrders.len) - 1); key >= 0 ; key--){
					var order = new this.$web3.eth.Contract(Order.abi,QueryOrders.orders[key]);
					var isLink = await order.methods.isLink().call()
					if(isLink){
						const ethContractAddress= await order.methods.linkedOrderAddress().call()
						order = new this.ETHWeb3.eth.Contract(Order.abi, ethContractAddress);
						
						var orderState = await order.methods.orderState().call()
						
						var OnCreated = await order.methods.times(0).call();
						var OnCountDownStrat = await order.methods.times(4).call();
						var OnCountDownEnd = await order.methods.times(5).call();
						var CurrentProfitInfoTime = await order.methods.times(7).call();
						
						// usdt
						var usdt = this.$fromWei(await this.$contract.getData('usdtToken').methods.balanceOf(QueryOrders.orders[key]).call(),'','Mwei');
						// 是否完成排队
						var CurrentProfitInfo = (await order.methods.CurrentProfitInfo().call())[0];
						// 最新块的时间
						var currentTime = await this.$currentTime();
						
						if(stateType != 2){
							// 当前系统已经支付了多少
							var toHelpedAmount = this.$fromWei(await order.methods.toHelpedAmount().call(),'','Mwei');
							// 订单总量
							var totalAmount = this.$fromWei(await order.methods.totalAmount().call(),'','Mwei');
							// 当前帮助
							var currentPaymentedAmount = this.$BN(toHelpedAmount).add(this.$BN(usdt))
							// 当前占比
							var currentScale = this.$BN(currentPaymentedAmount*100).div(this.$BN(totalAmount)).toNumber()
						}else{
							// 订单总量
							var totalAmount = this.$fromWei(await order.methods.getHelpedAmountTotal().call(),'','Mwei');
							// 当前帮助
							var currentPaymentedAmount = this.$fromWei(await order.methods.getHelpedAmount().call(),'','Mwei');
							// 当前占比
							var currentScale = this.$BN(currentPaymentedAmount*100).div(this.$BN(totalAmount)).toNumber()
						}
						
						if(currentTime > OnCountDownStrat && orderState == '2'){
							var OrderCountdownStarts = true;
						}else{
							var OrderCountdownStarts = false;
						}
						
						if(orderState == '2' || orderState == '4' || orderState == '6'){
							var isComplete = false
						}else{
							var isComplete = true
						}
						
						if(stateType == 2){
							var type = 3
						}else if(orderState == '6' || orderState == '7' || (orderState == '5' && CurrentProfitInfo == '1')){
							var type = 2
						}else if(orderState == '2' || (orderState == '5' && CurrentProfitInfo != '1')){
							var type = 1
						}
						
						this.ProvideHelpList.push({
							order: QueryOrders.orders[key],
							totalAmount: totalAmount,
							state: orderState,
							currentScale: currentScale,
							cretaTime: OnCreated,
							timesDown: '00:00:00:00',
							OnCountDownEnd: OnCountDownEnd,
							CurrentProfitInfo: CurrentProfitInfoTime && currentTime > CurrentProfitInfoTime ? true : false,
							OrderCountdownStarts: OrderCountdownStarts,
							type: type,
							isComplete: isComplete,
						})
						
						this.ProvideHelpList.sort(function(a,b){
							return b.cretaTime - a.cretaTime
						})
						continue
					}
					
					var orderState = QueryOrders.states[key]
					
					var pow64 = this.$BN(2).pow(this.$BN(64));
					var pow32 = this.$BN(2).pow(this.$BN(32));
					var OnCreated = await order.methods.times(0).call();
					var OnCountDownStrat = this.$BN(QueryOrders.times[key]).mod(pow64).div(pow32).toNumber();
					var OnCountDownEnd = OnCountDownStrat + (3 * 3600);
					var CurrentProfitInfoTime = this.$BN(QueryOrders.times[key]).mod(pow32).toNumber()
					
					// usdt
					var usdt = this.$fromWei(await this.$contract.getData('usdtToken').methods.balanceOf(QueryOrders.orders[key]).call(),'','Mwei');
					// 是否完成排队
					var CurrentProfitInfo = (await order.methods.CurrentProfitInfo().call())[0];
					// 最新块的时间
					var currentTime = await this.$currentTime();
					
					if(stateType != 2){
						// 当前系统已经支付了多少
						var toHelpedAmount = this.$fromWei(await order.methods.toHelpedAmount().call(),'','Mwei');
						// 订单总量
						var totalAmount = this.$fromWei(QueryOrders.totalAmounts[key],'','Mwei');
						// 当前帮助
						var currentPaymentedAmount = this.$BN(toHelpedAmount).add(this.$BN(usdt))
						// 当前占比
						var currentScale = this.$BN(currentPaymentedAmount*100).div(this.$BN(totalAmount)).toNumber()
					}else{
						// 订单总量
						var totalAmount = this.$fromWei(await order.methods.getHelpedAmountTotal().call(),'','Mwei');
						// 当前帮助
						var currentPaymentedAmount = this.$fromWei(await order.methods.getHelpedAmount().call(),'','Mwei');
						// 当前占比
						var currentScale = this.$BN(currentPaymentedAmount*100).div(this.$BN(totalAmount)).toNumber()
					}
					
					if(currentTime > OnCountDownStrat && orderState == '2'){
						var OrderCountdownStarts = true;
					}else{
						var OrderCountdownStarts = false;
					}
					
					if(orderState == '2' || orderState == '4' || orderState == '6'){
						var isComplete = false
					}else{
						var isComplete = true
					}
					
					if(stateType == 2){
						var type = 3
					}else if(orderState == '6' || orderState == '7' || (orderState == '5' && CurrentProfitInfo == '1')){
						var type = 2
					}else if(orderState == '2' || (orderState == '5' && CurrentProfitInfo != '1')){
						var type = 1
					}

					this.ProvideHelpList.push({
						order: QueryOrders.orders[key],
						totalAmount: totalAmount,
						state: orderState,
						currentScale: currentScale,
						cretaTime: OnCreated,
						timesDown: '00:00:00:00',
						OnCountDownEnd: OnCountDownEnd,
						CurrentProfitInfo: CurrentProfitInfoTime && currentTime > CurrentProfitInfoTime ? true : false,
						OrderCountdownStarts: OrderCountdownStarts,
						type: type,
						isComplete: isComplete,
					})

					this.ProvideHelpList.sort(function(a,b){
						return b.cretaTime - a.cretaTime
					})
				}
					
			},
		},
		mounted() {
			this.init()
		},
		created(){
			xwNavigatorSetTitle.postMessage(this.title);
		}
	}
</script>

