		display: flex;
		flex-direction: column;
		padding-top: 0;
	}
	
	.orderSelect{
		display: flex;
		align-items: center;
		width: 100%;
		// background-color: #273446;
		font-size: 32px;
		// color: #fff;
		
		&>div{
			flex: 1;
			// border-bottom: 2px solid #516A89;
			padding: 38px 0;
		}
		
		.active{
			border-bottom: 2px solid #21C17A;
		}
	}
	
	.orderListBox{
		width: 100%;
		flex: 1;
		overflow-y: scroll;
		padding: 20px 36px 0;
			
		&>div{
			width: 100%;
			height: 190px;
			display: flex;
			margin-bottom: 20px;
			
			&>.MyHelpListBoxContentRight{
				flex: 1;
				height: 190px;
				// background-color: #273446;
				border-radius: 16px;
				position: relative;
				// box-shadow: 0 6px 4px #000;
				
				&>.MyHelpListBoxContentRightRate{
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
