					&>div{
						display: flex;
						align-items: center;
						line-height: 1.2;
						font-size: 16px;
						color: #A1BDE1;
						
						&>div{
							display: flex;
							margin-left: 8px;
							
							&>div{									
								font-size: 20px;
								display: flex;
								width: 18px;
								height: 26px;
								background-color: #18222F;
								border-radius: 4px;
								justify-content: center;
								align-items: center;
								position: relative;
								z-index: 1;
