								z-index: -1;
							}
						}						
					}
					
					&>span{
						font-size: 20px;
					}
					
					.green{
						color: #21C17A;
					}
					
					.yellow{
						color: #EFB635;
					}
					
					.gray{
						color: #3B4E66;
					}
				}
				
				&>.MyHelpListBoxContentRightContent{
					position: relative;
					display: flex;
					align-items: center;
					margin-top: 15px;
					justify-content: space-between;
					padding: 0 20px;
					
					&>div:nth-child(1){
						display: flex;
						align-items: center;
						
						.MyHelpListBoxContentRightCircle{
							width: 52px;
							height: 52px;
							display: flex;
							align-items: center;
							justify-content: center;
							overflow: hidden;
							position: relative;
							z-index: 2;
							border-radius: 50%;
							
							&>div:nth-child(2){
								position: absolute;
								width: 0%;
								height: 100%;
								background-color: #21C17A;
								left: 0px;
