								z-index: 1;
							}
							
							.yellowBackground{
								background-color: #EFB635!important;
							}
							
							.grayBackground{
								background-color: #3B4E66!important;
							}
							
							&>div:nth-child(3){
								position: absolute;
								width: 100%;
								height: 100%;
								border: 2px solid #21C17A;
								left: 0px;
								top: 0px;
								z-index: 1;
								border-radius: 50%;
							}
							
							.yellowBorder{
								border: 2px solid #EFB635!important;
							}
							
							.grayBorder{
								border: 2px solid #3B4E66!important;
							}
							
							.van-icon{
								color: #fff;
								z-index: 2;
								font-size: 15px;
