<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox authorizationManagementBox">
			<div class="usdtLogo">
				<img src="../../../assets/Ttoken.png" />
			</div>
			<div class="CurrentAuthorization colorA1BDE1">
				<span>{{$t('AuthorizationManagementText10')}}</span>
				<div>{{USDTAuthorizedAccess}} USDT</div>
				<p>{{$t('AuthorizationManagementText01')}}</p>
			</div>
			<div class="authorizationManagementText">
				<p v-if="USDTAuthorizedAccess > 0">{{$t('AuthorizationManagementText02')}}</p>
				<p v-if="USDTAuthorizedAccess > 0">{{$t('AuthorizationManagementText03')}}</p>
				<p v-if="USDTAuthorizedAccess === 0">{{$t('AuthorizationManagementText04')}}</p>
				<div class="cause" v-if="USDTAuthorizedAccess > 0" @click="pushHref">
					<span>{{$t('AuthorizationManagementText05')}}</span>
					<p>https://github.com/ethereum/EIPs/issues/20#issuecomment-263524729</p>
				</div>
				<div v-if="USDTAuthorizedAccess === 0">{{$t('AuthorizationManagementText06')}}</div>
			</div>
			<div class="checkbox" @click="checked = !checked" v-if="USDTAuthorizedAccess === 0">
				<icon name="success" :class="checked ? 'active' : ''"></icon>
				<span>{{$t('AuthorizationManagementText07')}}</span>
			</div>
			<button :disabled="USDTAuthorizedAccess > 0 ? false : !checked" @click="send" v-ButClickLimit>{{USDTAuthorizedAccess === 0 ? $t('AuthorizationManagementText08') :$t('AuthorizationManagementText09')}}</button>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import { Icon } from 'vant'
	export default{
		components:{ navBar , Icon },
		data(){
			return {
				title: this.$t('USDTAuthorizationManagement'),
				checked: false,
				USDTAuthorizedAccess: '0',
			}
		},
		mounted() {
			this.init()
		},
		methods:{
			init(){
				// usdt授权额度
				this.$contract.getData('usdtToken').methods.allowance(this.$address,this.$contract.getData('controller')._address).call({
					from:this.$address,
				},(err,result)=>{
					this.USDTAuthorizedAccess = this.$fromWei(result,'','Mwei');
				})
			},
			send(){
				if(this.USDTAuthorizedAccess === 0){
					var value = '10000'
				}else if(this.USDTAuthorizedAccess > 0){
					var value = '0'
				}
				this.$contract.getData('usdtTokenI').methods.approve(this.$contract.getData('controller')._address,this.$web3.utils.toWei(value,'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' ,async result=>{
					this.init()
				})
			},
			pushHref(){
				window.location.href = "https://github.com/ethereum/EIPs/issues/20#issuecomment-263524729"
			}
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.authorizationManagementBox{
		padding: 0 40px;
		
		.usdtLogo{
			width: 128px;
			height: 128px;
			background-color: #1ba27a;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 auto 40px;

			&>img{
				width: 60%;
			}
		}
		
		.CurrentAuthorization{
			font-size: 32px;
			
			&>div{
				font-size: 40px;
				margin: 12px 0 0;
			}
			
			&>p{
				font-size: 36px;
				margin: 40px 0;
			}
		}
		
		.authorizationManagementText{
			padding: 36px;
			
			font-size: 28px;
			text-align: left;
			border-radius:0px 40px 0px 40px;
			
			&>p{
				text-indent: 1em;
			}
			
			&>.cause{
				margin-top: 20px;
				word-break: break-word;
				position: relative;
				line-height: 1.3;
				color: #A1BDE1;
				
				&>p{
					text-indent: 3em;
				}
				
				&>span{
					color: #4D6481;
					position: absolute;
					left: 0;
				}
			}
		}
		
		.checkbox {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 40px;
			font-size: 30px;
		
			&>i {
				width: 40px;
				height: 40px;
				line-height: 40px;
				color: transparent;
				margin-right: 15px;
				border-radius: 50%;
				border: 2px solid #4D6481;
				&.active {
					// color: #fff;
					background-color: transparent;
				}
			}
			&>span {
				font-size: 28px;
				text-align: left;
			}
		}
		
		button{
			width: 400px;
			height: 96px;
			border-radius: 50px;
			background-color: #21C17A;
			font-size: 40px;
			color: #fff;
			border: none;
			margin-top: 100px;
		}
		
		button:disabled{
			opacity: .5;
		}
	}
</style>
