<template>
	<div class="van-tabbar-page box">
		<!-- <div class="indexTopBg"></div> -->
		<div class="getHelp">
			<div class="getHelpDetails" @click="App_Push('/incomeDetail')">
				<div class="topLeft">
					<img class="lv" v-if="lv == '1'" src="../../../assets/lv1.png" />
					<img class="lv" v-if="lv == '2'" src="../../../assets/lv2.png" />
					<img class="lv" v-if="lv == '3'" src="../../../assets/lv3.png" />
					<img class="lv" v-if="lv == '4'" src="../../../assets/lv4.png" />
					<img class="lv" v-if="lv == '5'" src="../../../assets/lv5.png" />
					<img class="vip" src="../../../assets/vip.png" />
				</div>
				<div class="topRight">
					<span>{{$t('IncomeDetails')}}</span>
					<img src="../../../assets/doubleRight.png" />
				</div>
				<div class="reveiceIncome">
					<span>{{rewardAmount}}</span>
					<div>{{$t('CurrentIncome')}}</div>
				</div>
				<div class="orderInfo colorA1BDE1">
					<div>
						<span>{{$t('Available01')}}</span>
						<span>{{Amount}}</span>
					</div>
					<span></span>
					<div>
						<span>{{$t('TotalRevenue')}}</span>
						<span>{{totalRewardedAmount}}</span>
					</div>
				</div>
				<div class="incomeBg"></div>
			</div>
			
			<div class="income colorA1BDE1_1">
				<div class="title">
					<span>{{$t('extract')}}</span>
				</div>
				<div class="input">
					<div>
						<input type="number" :placeholder="$t('PleaseEnterMultipleOf10')" v-model="value" @input="input">
					</div>
					<span>USDT</span>
				</div>
					<!-- <div class="input">
						<input type="number" placeholder="消耗燃料" v-model="ConsumeDT" disabled>
						<span>DT</span>
					</div> -->
				<div class="Underline ConsumeDT">
					<span>{{$t('FuelConsumption')}}</span>
					<span>{{ConsumeDT}} DT</span>
				</div>
				<div class="AvailableDT">
					<span>{{$t('Available')}}：</span>
					<span>{{DT}} DT</span>
				</div>
				<!-- <span>可用 {{DT}} DT</span> -->
				<Button color="linear-gradient(44deg, #21C17A, #21C1A8)" @click="createOrder" v-ButClickLimit>{{$t('Commit')}}</Button>
				<div class="checkbox" @click="rule = true">
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
			</div>
		</div>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div class="colorA1BDE1">
				<div class="ruleTop">
					<div>
						<icon name="fail"></icon>
					</div>
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
				<div class="ruleBottom">
					<p>1、{{$t('ReceivingInstructionsText01')}}；</p>
					<p>2、{{$t('ReceivingInstructionsText02')}}；</p>
					<p>3、{{$t('ReceivingInstructionsText03')}}；</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Button , Toast , Overlay } from 'vant'
	import Order from "../../../static/Order.json"
	export default {
		components:{ Icon , Button , Overlay },
		data () {
			return {
				active: 0,
				orderList: [],
				times: '',
				teamAchievements: 0,
				TeamMemberTotal: 0,
				dlevel: '',
				rewardAmount: 0,
				lv: '',
				totalRewardedAmount: 0,
				checked: false,
				ConsumeDT: 0,
				DT: 0,
				value: '',
				Amount: 0,
				rule: false,
				lastOrder: false,
			}
		},
		methods:{
			async init(){
				
				// DT数量
				// this.$contract.getData('referenceToken').methods.balanceOf(this.$address).call((err,result)=>{
				// 	this.DT = this.$fromWei(result)
				// })
				this.IWeb3.eth.getBalance(this.$address).then(result=>{
					this.DT = this.$fromWei(result)
				})
				
				// 消耗燃料公式
				this.$contract.getData('config').methods.WithdrawCostProp().call((err,result)=>{
					this.WithdrawCostProp = result;
				})
				this.$contract.getData('config').methods.USDTToDTProp().call((err,result)=>{
					this.USDTToDTProp = result;
				})
				
				// 当前可领取收益/累计领取收益
				this.$contract.getData('reward').methods.RewardInfo(this.$address).call((err,result)=>{
					var RewardInfo = result 
					
					this.rewardAmount = this.$fromWei(RewardInfo.rewardAmount,'','Mwei')
					this.totalRewardedAmount =  this.$fromWei(RewardInfo.totalRewardedAmount,'','Mwei')
					
					if(this.rewardAmount < this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')){
						this.Amount = this.rewardAmount
					}else{
						this.Amount = this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')
					}
				})
				
				// 等级
				this.$contract.getData('counterManager').methods.UpgradeDLevel().call((err,result)=>{
					this.lv = result.origin
				})
				
				// 这里的判断条件写成了，从当前时间开始的24内是否挂单，这个逻辑是有问题的 by Mr.K
				// let now = new Date(new Date().toLocaleDateString()).getTime() / 1000,
				// 	morn = (new Date(new Date().toLocaleDateString()).getTime() / 1000) + 24 * 60 * 60;
				
				// //是否今天挂了单
				// this.$contract.getData('controller').methods.GetOrder(this.$address,0).call(async (err,result) => {
				// 	if(Number(result.total)){
				// 		let id = Number(result.total) - 1;
				// 		let getOrder = (await this.$contract.getData('controller').methods.GetOrder(this.$address,id).call()).order;
				// 		let order = new this.$web3.eth.Contract(Order.abi,getOrder);
				// 		let orderCreateTime = Number(await order.methods.times(0).call());
						
				// 		if(orderCreateTime > now && orderCreateTime < morn) return this.lastOrder = true;
				// 	}else{
				// 		this.lastOrder = false;
				// 	}
				// });
				
				// 去除今日挂单后才可提取的限制条件 by Mr.K 20201028 02:41
				this.lastOrder = true;
			},
			input(){
				var value = Number(this.value);
				if(value == '' ||  value == 0){
					this.value = ''
					this.ConsumeDT = ''
					return 
				}else{
					this.ConsumeDT = this.$fromWei(this.$BN(this.$toWei((value).toString(),'Mwei')).mul(this.$BN(this.WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(this.USDTToDTProp)).toString())
				}
			},
			async createOrder(){
				var value = Number(this.value)
				if(value == '' ||  value == 0){
					return Toast.fail(this.$t('PleaseEnterTheWithdrawalIncome'))
				}else if(!this.rewardAmount){
					return Toast.fail(this.$t('warningText08')+'0');
				}else if(this.rewardAmount < this.OrderAmountGranularity){
					return Toast.fail(this.$t('warningText09')+this.OrderAmountGranularity);
				}else if(this.value > this.Amount){
					return Toast.fail(this.$t('warningText10'));
				}else if(this.value % 10 != 0){
					return Toast.fail(this.$t('PleaseEnterMultipleOf10'));
				}else if(this.ConsumeDT > this.DT){
					return Toast.fail(this.$t('warningText11'));
				}else if(!this.lastOrder){
					return Toast.fail('没有当天的订单');
				}
				var state = await this.$contract.getData('controller').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).call({from: this.$address,value: this.$toWei(this.ConsumeDT.toString())})
				// console.log(state)
				if(state == '0'){					
					this.$contract.getData('controllerI').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).send({
						from: this.$address,
						value: this.$toWei(this.ConsumeDT.toString())
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					})
					.on('receipt' , result=>{
						this.value = ''
						this.ConsumeDT = ''
						this.init()
						// this.App_Push('./order')
					})
				}else if(state == 6){
					Toast.fail(this.$t('warningText12'))
				}
			},
		},
		mounted(){
			this.init()
		},
		destroyed() {
			clearInterval(this.times);
		}
	}
</script>

