<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox bgColor273446 luckPlayerBox">
			<div class="item" v-for="(item, index) in list" :key="index">
				<div>
					<span>{{ item.name }}</span>
					<span>{{ `${item.address.slice(0, 7)}...${item.address.slice(-7)}` }}</span>
				</div>
				<span>{{ item.amount }} <span>USDT</span></span>
			</div>
			<not-record v-if="!list.length" :text="NoLuckyPlayers"></not-record>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import notRecord from '@/components/not-record'
	
	export default {
		components: { navBar, notRecord },
		data() {
			return {
				list: [],
				title: this.$t('LuckyPlayer'),
				NoLuckyPlayers: this.$t('NoLuckyPlayers'),
			}
		},
		mounted() {
			this.$web3.eth.getBlockNumber().then(res => {
				let block = res < 100000 ? 0 : res - 100000
				
				this.$contract.getData('assertPoolAwardsWs').events.Log_Luckdog({
					fromBlock: block
				}).on('data', async res => {
					let name = await this.$contract.getData('relationship').methods.AddressToNickName(res.returnValues.who).call()
					
					this.list.push({
						name: this.$web3.utils.hexToString(name),
						address: res.returnValues.who,
						amount: this.$numFixed(this.$fromWei(res.returnValues.awardsTotal, '', 'mwei'), 2)
					})
				})
			})
		}
	}
</script>

<style lang="scss" scoped>
	.item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		// color: #8FACD2;
		font-size: 32px;
		margin-bottom: 30px;
		padding-bottom: 20px;
		border-bottom: 1px solid #516A89;
		&>span>span {
			font-size: 20px;
		}
		&>div {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			&>span:last-child {
				// color: #7489A5;
				font-size: 28px;
				margin-top: 10px;
			}
		}
	}
	.navBox {
		padding: 38px 20px 0;
		// background-color: #273446;
	}
</style>
