<template>
	<div class="compenstate box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox compensationFeesBox">
			<div class="compenstateCircle">
				<div>{{ (DT / 100000) * 100 }}%</div>
				<span>当前补偿总量： {{DT}} DT</span>
			</div>
			<div class="compenstateInfo colorA1BDE1">
				<div>补偿说明</div>
				<p></p>
			</div>
			<p>当前可领取：{{available}} DT</p>
			<Button @click="send" v-ButClickLimit>{{$t('receive')}}</Button>
		</div>
	</div>
</template>

<script>
	import { Button , Toast } from 'vant'
	import navBar from '@/components/nav-bar'
	
	export default{
		components:{ navBar , Button },
		data(){
			return {
				title: '手续费补偿计划',
				SettlementCompensate: 0,
				available: 0,
				DT: 0,
			}
		},
		methods:{
			async init(){
				// DT数量
				// this.$contract.getData('referenceToken').methods.balanceOf(this.$contract.getData('airDropFee')._address).call((err,result)=>{
				// 	this.DT = this.$fromWei(result)
				// 	// console.log(this.DT)
				// })
				this.IWeb3.eth.getBalance(this.$contract.getData('airDropFee')._address).then(result=>{
					this.DT = this.$fromWei(result)
				})

				this.$contract.getData('airDropFee').methods.availableAmount(this.$address).call({
					from: this.$address,
				},async (err,result)=>{
					this.available = this.$fromWei(result.dt)
					// console.log(result)
				})
				
				
			},
			pushCompensateRecord(){
				this.App_Push('/compensateRecord')
			},
			send(){
				this.$contract.getData('airDropFeeI').methods.withdrawAirdrop().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
		},
		mounted() {
			this.init()
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>
<style>
	.compenstate i{
		font-size: 45px!important;
		/* color: #A1BDE1; */
	}
</style>
<style lang="scss" scoped>
	.compensationFeesBox{
		padding: 50px 24px 0;
		
		.compenstateCircle{
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 28px;
			// color: #FFFFFF;
			
			&>div{				
				width: 240px;
				height: 240px;
				border-radius: 50%;
				background: linear-gradient(180deg,#21C1A8 0%,#21C17A 100%);
				font-size: 60px;
				margin-bottom: 28px;
				line-height: 240px;
			}
		}
		
		&>p{
			color: #21C17A;
			font-size: 36px;
			margin: 60px 0;
		}
		
		.compenstateInfo{
			width: 100%;
			// height: 272px;
			border-radius: 20px;
			// background-color: #273446;
			display: flex;
			flex-direction: column;
			align-items: center;
			margin: 70px 0 0px;
			padding: 20px 36px;
			// color: #A1BDE1;
		
			// &>p{
			// 	border-right: 2px solid #18222F;
			// }
			
			&>div{
				font-size: 28px;
			}
		}
		
		&>button{
			width: 400px;
			height: 96px;
			background: linear-gradient(270deg,#21C1A8 0%,#21C17A 100%);
			border: none;
			border-radius: 50px;
			font-size: 40px;
			color: #fff;
		}
	}
</style>
