<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox upLv">
			<div class="upLvTop">
				<img v-if="origin == 0" src="../../../assets/upLv00.png" />
				<img v-else-if="origin == 1" src="../../../assets/upLv01.png" />
				<img v-else-if="origin == 2" src="../../../assets/upLv02.png" />
				<img v-else-if="origin == 3" src="../../../assets/upLv03.png" />
				<img v-else-if="origin == 4" src="../../../assets/upLv04.png" />
				<img v-else-if="origin == 5" src="../../../assets/upLv05.png" />
			</div>
			<p class="colorA1BDE1">{{$t('currentLevel')}}</p>
			<span>{{Number(origin) ? 'D'+origin : 'VIP'}}</span>
			<div class="upLvBannerText colorA1BDE1" v-if="origin == 0">{{$t('EnjoyShortcutsWithoutWaiting')}}</div>
			<Button @click="payLv" v-if="origin == 0" v-ButClickLimit>{{$t('UpgradeNow')}}</Button>
			<div class="upLvDetailsText" @click="upLvDetails = true" v-if="origin == 0">
				<span>{{$t('LearnMore')}}</span>
			</div>
			<div class="upLvIdentityIcon colorA1BDE1" v-if="origin != 0">
				<span>{{$t('levelIcon')}}</span>
				<div>
					<div>						
						<div>
							<img src="../../../assets/upLv00.png" />
							<span>VIP</span>
						</div>
						<div>
							<img src="../../../assets/upLv01.png" />
							<span>D1</span>
						</div>
						<div>
							<img src="../../../assets/upLv02.png" />
							<span>D2</span>
						</div>
					</div>
					<div>
						<div>
							<img src="../../../assets/upLv03.png" />
							<span>D3</span>
						</div>
						<div>
							<img src="../../../assets/upLv04.png" />
							<span>D4</span>
						</div>
						<div>
							<img src="../../../assets/upLv05.png" />
							<span>D5</span>
						</div>
					</div>
				</div>
			</div>
		</div>
		
		
		<overlay :show="upLvDetails" class="upLvDetails" @click="upLvDetails = false">
			<div @click.stop class="upLvText colorA1BDE1">
				<div>
					<div></div>
					<span>{{$t('LevelUpgradePayment')}}{{dlv1Prices}} USDT{{$t('UpgradeTo')}}D1；</span>
				</div>
				<div>
					<div></div>
					<span>{{$t('EffectiveImmediatelyAfterBlockConfirmation')}}。</span>
				</div>
			</div>
		</overlay>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='dlv1Prices' 
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
	</div>
</template>

<script>
	import Authorization from '@/components/Authorization.vue'
	import navBar from '@/components/nav-bar'
	import { Button , Toast , Overlay } from 'vant'
	export default{
		components:{ navBar , Button , Overlay , Authorization },
		data(){
			return {
				title: this.$t('levelUpgrade'),
				name: '',
				origin: '',
				DT: 0,
				upLvDetails: false,
				dlv1Prices: 0,
				authorizedAccess: false,
				authorizationEnds: false,
				usdt: 0,
			}
		},
		methods:{
			async init(){
				this.$contract.getData('relationship').methods.AddressToNickName(this.$address)
				.call((err, res) => {
					if (!err) {
						let name = this.$web3.utils.hexToString(res)
						this.name = name === '' ? 'DeFi Era' : name
					}
				})
				
				this.$contract.getData('counterManager').methods.UpgradeDLevel()
				.call({from:this.$address},(err, res) => {
					if (!err) {
						// 原等级
						this.origin = res.origin;
					}
				})
				
				this.$contract.getData('counterManagerState').methods.dlv1Prices().call((err,result)=>{
					this.dlv1Prices = this.$fromWei(result,'','Mwei')
				})
				
				this.$contract.getData('usdtToken').methods.allowance(this.$address,this.$contract.getData('counterManager')._address).call({
					from:this.$address,
				},(err,result)=>{
					this.USDTAuthorizedAccess = this.$fromWei(result,'','Mwei')
				})
				
				this.$contract.getData('usdtToken').methods.balanceOf(this.$address).call((err,result)=>{
					this.usdt = this.$fromWei(result,'','Mwei');
				})
			},
			async payLv(){
				if(this.origin >= 1){
					return Toast.fail(this.$t('YouAre')+'D'+this.origin+this.$t('level'));
				}else if(this.usdt < this.dlv1Prices){
					return Toast.fail(this.$t('InsufficientBalance'));
				}
				// else if(this.USDTAuthorizedAccess < this.dlv1Prices){
				// 	return this.authorizedAccess = true;
				// }
				var state = await this.$contract.getData('counterManager').methods.PaymentDLevelOne().call({from:this.$address})
				if(!state){
					return
				}
				this.$contract.getData('counterManagerI').methods.PaymentDLevelOne().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.App_Pop()
				})
			},
			authorizedAccessFun(){
				this.$contract.getData('usdtTokenI').methods.approve(this.$contract.getData('counterManager')._address,this.$web3.utils.toWei(this.dlv1Prices.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , async result=>{
					this.authorizedAccess = false;
					this.authorizationEnds = true;
					this.USDTAuthorizedAccess = this.$fromWei(await this.$contract.getData('usdtToken').methods.allowance(this.$address,this.$contract.getData('counterManager')._address).call({from:this.$address}),'','Mwei')
					this.payLv()
				})
			},
			end(){
				this.authorizedAccess = false;
			},
		},
		mounted() {
			this.init()
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.upLv{
		padding: 70px 40px 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		
		&>.upLvTop{
			width: 275px;
			height: 240px;
			background-image: url(../../../assets/upLvTopBg.png);
			background-size: 275px 240px;
			margin-bottom: 16px;
			display: flex;
			justify-content: center;
			padding: 62px 0 100px;
			
			&>img{
				height: 100px;
			}
		}
		
		&>span{
			font-size: 40px;
			color: #21C17A;
		}
		
		&>p{
			font-size: 40px;
			// color: #A1BDE1;
			margin: 40px 0 20px;
		}
		
		&>.upLvBannerText{
			font-size: 60px;
			// color: #A1BDE1;
			margin: 140px 0;
		}
		
		&>button{
			width: 400px;
			height: 96px;
			font-size: 40px;
			border-radius: 50px;
			background-color: #21C17A;
			color: #fafafa;
			border: none;
		}
		
		&>.upLvDetailsText{
			// color: #A1BDE1;
			font-size: 32px;
			margin-top: 40px;
		}
		
		&>.upLvIdentityIcon{
			margin-top: 166px;
			font-size: 48px;
			// color: #A1BDE1;
			width: 100%;
			
			&>div{
				padding: 40px;
				// background-color: #273446;
				border-radius: 20px;
				margin-top: 20px;
				
				&>div{
					display: flex;
					justify-content: space-between;
					margin-bottom: 40px;
					
					&>div{
						display: flex;
						flex-direction: column;
						align-items: center;
						// justify-content: space-around;
						justify-content: flex-end;
						
						&>img{
							width: 104px;
							margin-bottom: 40px;
						}
					}
				}
				
				&>div:last-child{
					margin-bottom: 0;
				}
			}
		}
	}
	
	.upLvDetails{
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 40px;
	}
	.upLvText{
		width: 100%;
		height: 200px;
		border-radius: 20px;
		// background-color: #273446;
		font-size: 32px;
		// color: #A1BDE1;
		text-align: left;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 40px 0 40px 34px;
		margin: 56px 0 118px;
		
		&>div{
			display: flex;
			align-items: center;
			
			&>div{
				width: 12px;
				height: 12px;
				border: 2px solid #21C17A;
				border-radius: 50%;
				margin-right: 16px;
			}
		}
	}
</style>
