<script>
	// import eruda from 'eruda'
	import { Overlay , Toast } from 'vant'
	import selfTabbar from '@/components/self-tabbar'
	export default {
		components: { selfTabbar , Overlay },
		data() {
			return {
				name: true,
				ResolveBreakerDTAmount: '',
				show: false,
				IsBreaker: false,
				DT: 0,
				pauseable: false,
				death: true,
				guide: this.$guide
			}
		},
		async mounted() {
			// eruda.init()
			
			if (localStorage.getItem('theme') === 'sun') {
				import('@/theme/theme-sun.scss')
			} else {
				localStorage.setItem('theme','moon')
				import('@/theme/theme-moon.scss')
			}
			// if(localStorage.getItem('theme') === 'white'){
			// 	import('@/theme/theme-white.scss')
			// }else{
			// 	localStorage.setItem('theme','white')
			// 	import('@/theme/theme-white.scss')
			// }
			
			let locale = localStorage.getItem('locale')
			if (locale) {
				this.$i18n.locale = locale
			}
			
			// 是否在黑名单
			// this.IsBreaker = await this.$contract.getData('controller').methods.IsBreaker(this.$address).call();
			this.$contract.getData('controller').methods.IsBreaker(this.$address).call(async (err,result)=>{
				if(!err){
					this.IsBreaker = result
					if(this.IsBreaker){
						this.ResolveBreakerDTAmount = this.$fromWei(await this.$contract.getData('config').methods.ResolveBreakerDTAmount().call());
						this.DT = this.$fromWei(await this.IWeb3.eth.getBalance(this.$address).then())
						if(this.$route.name == 'exchange'){
							this.show = false;
						}else{
							this.show = true;
						}
					}else{
						this.show = false;
					}
				}
			});
			
			this.$contract.getData('assertPoolAwards').methods.pauseable().call((err,result)=>{
				this.pauseable = result;
			})
			
			this.$contract.getData('assertPoolAwardsState').methods.death().call((err,result)=>{	
				this.death = result;
			})
		},
		methods:{
			async ResolveBreaker(){// 解除黑名单
				if(this.DT < this.ResolveBreakerDTAmount){
					return Toast.fail('DT不足');
				}
				this.$contract.getData('controllerI').methods.ResolveBreaker().send({
					from : this.$address,
					value: this.$toWei(this.ResolveBreakerDTAmount.toString())
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.show = false;
					this.IsBreaker = false;
					// this.App_Push('/index')
				})
			},
			openHome(href){
				this.App_Push(href)
			}
		},
		watch:{
			async $route(){
				if(this.IsBreaker){					
					if(this.$route.name == 'exchange'){
						this.show = false;
					}else{
						this.show = true;
					}
				}else{
					this.show = false;
				}
			}
		},
	}
</script>
<style lang="scss" scoped>
