				&>img:nth-child(1){
					width: 751px;
					height: 752px;
					animation: myfirst 5s infinite;
					animation-timing-function: linear;
				}
				
				&>img:nth-child(2){
					width: 232px;
					height: 217px;
					position: absolute;
					left: 50%;
					top: 260px;
					transform: translateX(-50%);
				}
				
				@keyframes myfirst{
					100% {transform: rotate(360deg);}
				}
				
				&>span{
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					bottom: 80px;
				}
			}
		}
	}
	
	.guide{
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 22px;
		
		&>div{
			width: 100%;
			background-color: #2A405C;
			padding: 0 10px 0 16px;
			border-radius: 10px;
			
			&>.guideTitle{
