<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox exchangeRecordNavBox">
			<div class="colorA1BDE1">
				<div v-for="(item, index) in list" :key="index">
					<div>
						<span>{{$t('SwapSuccess')}}</span>
						<span>{{ item.date }}</span>
					</div>
					<div>
						<div>
							<span>-{{ item.usdt }} USDT</span>
							<span>+{{ item.dt }} DT</span>
						</div>
						<img v-lazy="require('../../../assets/changeRecord_next.png')">
					</div>
				</div>
				<not-record :text="NoRecord" v-if="!list.length"></not-record>
			</div>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import NotRecord from '@/components/not-record'
	
	export default {
		components: { navBar, NotRecord },
		data() {
			return {
				title: this.$t('SwapRecords'),
				NoRecord: this.$t('NoRecord'),
				list: [
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' },
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' },
					// { usdt: '100.00', dt: '5000.00', date: '2020-02-17 01:02' }
				]
			}
		},
		async mounted() {
			var address = this.$address
			this.$contract.getData('swapWs').events.Log_Swaped({
				fromBlock: 0,
				filter: {owner: address}
			}, function(error, event){ 
				// console.log(event)
			})
			.on('data', async event => {
				// console.log(event)
				this.list.unshift({
					usdt: this.$fromWei(event.returnValues.inAmount,'','Mwei'),
					dt: this.$fromWei(event.returnValues.outAmount),
					date: this.$toTime(event.returnValues.time,'Y-M-D H:i'),
				})
			})
			.on('changed', function(event){
			})
			.on('error', console.error);
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		padding: 0 22px 58px;
		&>div {
			width: 100%;
			height: 100%;
			// color: #A1BDE1;
			font-size: 26px;
			border-radius: 10px;
			// background-color: #273446;
			padding: 38px 30px 0;
			overflow-y: auto;
			&>.not_record {
				border: none;
			}
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 22px;
				padding-bottom: 10px;
				border-bottom: 1px solid #17212E;
				&>div {
					display: flex;
					&>div span:first-child {
						margin-bottom: 2px;
					}
					&:first-child {
						flex-direction: column;
						align-items: flex-start;
						&>span:first-child {
							color: #21C17A;
						}
					}
					&:last-child {
						align-items: center;
						&>div {
							display: flex;
							flex-direction: column;
							align-items: flex-end;
						}
						&>img {
							width: 9px;
							height: 17px;
							margin-left: 32px;
						}
					}
				}
			}
		}
	}
</style>
