<template>
	<tabbar
		v-if="$route.meta.tabbar"
		:active="active"
		@change="onChange"
		:inactive-color='inactiveColor'
		router
	>
		<tabbar-item name="index" to="/index">
			<span :class="{active:active == 'index'}">{{$t('home')}}</span>
			<img v-if="active != 'index' && theme == 'moon'" src="../assets/index.png" slot='icon' />
			<img v-else-if="active == 'index' && theme == 'moon'" src="../assets/indexActive.png" slot='icon' />
			<img v-else-if="active != 'index' && theme == 'sun'" src="../assets/indexSun.png" slot='icon' />
			<img v-else-if="active == 'index' && theme == 'sun'" src="../assets/indexSunActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="income" to="/income">
			<span :class="{active:active == 'income'}">{{$t('income')}}</span>
			<img v-if="active != 'income' && theme == 'moon'" src="../assets/income.png" slot='icon' />
			<img v-else-if="active == 'income' && theme == 'moon'" src="../assets/incomeActive.png" slot='icon' />
			<img v-else-if="active != 'income' && theme == 'sun'" src="../assets/incomeSun.png" slot='icon' />
			<img v-else-if="active == 'income' && theme == 'sun'" src="../assets/incomeSunActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="exchange" to="/exchange">
			<span :class="{active:active == 'exchange'}">{{$t('swap')}}</span>
			<img v-if="active != 'exchange' && theme == 'moon'" src="../assets/exchange.png" slot='icon' />
			<img v-else-if="active == 'exchange' && theme == 'moon'" src="../assets/exchangeActive.png" slot='icon' />
			<img v-else-if="active != 'exchange' && theme == 'sun'" src="../assets/exchangeSun.png" slot='icon' />
			<img v-else-if="active == 'exchange' && theme == 'sun'" src="../assets/exchangeSunActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="setting" to="/setting">
			<span :class="{active:active == 'setting'}">{{$t('setting')}}</span>
			<img v-if="active != 'setting' && theme == 'moon'" src="../assets/setting.png" slot='icon' />
			<img v-else-if="active == 'setting' && theme == 'moon'" src="../assets/settingActive.png" slot='icon' />
			<img v-else-if="active != 'setting' && theme == 'sun'" src="../assets/settingSun.png" slot='icon' />
			<img v-else-if="active == 'setting' && theme == 'sun'" src="../assets/settingSunActive.png" slot='icon' />
		</tabbar-item>
	</tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'

export default {
  name: 'self-tabbar',
  components: { Tabbar, TabbarItem },
  data() {
    return {
      active: 'index',
	  theme: localStorage.getItem('theme') == null ? 'moon' : localStorage.getItem('theme'),
	  // inactiveColor: localStorage.getItem('theme') == 'moon' ? '#A1BDE1' : '#333333',
	  inactiveColor: localStorage.getItem('theme') == null ? '#A1BDE1' : localStorage.getItem('theme') == 'sun' ? '#333' : '#A1BDE1',
    }
  },
  mounted() {
    this.active = this.$route.name
  },
  methods:{
	onChange(event) {
	    this.active = event
	}
  },
  watch:{
	  $route(){
		  this.active = this.$route.name
	  }
  }
}
</script>

<style lang="scss" scoped>
.van-tabbar{
	// background-color: #273446;
	border: none;
}
.van-tabbar:after{
	border: none;
}
.van-iconfont{
  font-size: 22px !important;
}
.van-tabbar-item__icon img{
	height: 22px;
}
.active{
	color: #21C17A!important;
}
// .iconHome{
// 	font-size: 48px !important;
// }

</style>