<script>
import { Rate , Popup , Toast , Icon , Overlay , PullRefresh } from 'vant';
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
import 'swiper/js/swiper.min.js'
import Order from "../../static/Order.json"
import selfIcon from '@/components/self-icon'
export default {
	components: { Rate , Popup , Icon , Overlay , selfIcon , PullRefresh },
	data () {
		return {
			value: 1,
			active: 1,
			ProvideHelpList: [],
			times: '',
			totalOut: 0,
			totalIn: 0,
			lucky: false,
			luckyAmout: '',
			yubei: 0,
			baowu: 0,
			jiangchi: 0,
			huodong: 0,
			isInCountdowning: false,
			name: '',
			deadlineTimes: '00:00:00',
			totalAddresses: 0,
			ranshao: 0,
			language: 0,
			currentTime: 0,
			theme: localStorage.getItem('theme'),
			isLoading: false,
			count: 0,
		}
	},
	methods:{
		switchLanguage(type){
			localStorage.setItem('locale',type)
			this.$i18n.locale = type
		},
		async init(){
			this.$contract.getData('assertPoolState').methods.availTotalAmouns(1).call((err,result)=>{
				this.yubei = Math.floor(this.$fromWei(result,'','Mwei'));
			})
			
			this.$contract.getData('assertPoolState').methods.availTotalAmouns(2).call((err,result)=>{
				this.baowu = Math.floor(this.$fromWei(result,'','Mwei'));
			})
			
			// 6月15日,由于比例错误，大奖池与探索基金对调
			this.$contract.getData('assertPoolState').methods.availTotalAmouns(4).call((err,result)=>{
				this.jiangchi = Math.floor(this.$fromWei(result,'','Mwei'));
			})
			this.$contract.getData('assertPoolState').methods.availTotalAmouns(3).call((err,result)=>{
				this.huodong = Math.floor(this.$fromWei(result,'','Mwei'));
			})
			
			// 累计提供帮助/累计获得帮助
			this.$contract.getData('phoenix').methods.GetInoutTotalInfo(this.$address).call((err,result)=>{
				this.totalOut = this.$fromWei(result.totalOut,'','Mwei')
				this.totalIn = this.$fromWei(result.totalIn,'','Mwei')
			})
			
			// 地址总量
			this.$contract.getData('relationship').methods.totalAddresses().call((err,result)=>{
				this.totalAddresses = result 
				// console.log(this.totalAddresses)
			})
			
			// 燃烧DT
			// this.$contract.getData('referenceToken').methods.balanceOf('0x0000000000000000000000000000000000000000').call((err,result)=>{
			// 	this.ranshao = this.$fromWei(result)
			// })
			this.IWeb3.eth.getBalance('0x0000000000000000000000000000000000000000').then(result=>{
				this.ranshao = Math.floor(this.$fromWei(result))
			})
			
			// 是否需要暂停
			this.$contract.getData('assertPoolAwards').methods.pauseable().call(async (err,result)=>{
				if(result){
					// 是否是幸运儿
					var IsLuckDog = await this.$contract.getData('assertPoolAwards').methods.IsLuckDog(this.$address).call();
					if(IsLuckDog.isluckDog && IsLuckDog.canwithdrawable){
						this.lucky = true
						this.luckyAmout = this.$fromWei(IsLuckDog.award,'','Mwei')
					}else{
						this.lucky = false
						var death = await this.$contract.getData('assertPoolAwardsState').methods.death().call();
						if(death){
							this.$router.replace('/drawCompensate')
						}
					}
				}
			})
			
			// 最新块的时间
			this.currentTime = await this.$currentTime()
			
			// 是否开启倒计时
			this.$contract.getData('assertPoolAwardsState').methods.isInCountdowning().call(async (err,result)=>{
				if(result){
					this.isInCountdowning = true
					this.deadlineTime = await this.$contract.getData('assertPoolAwardsState').methods.deadlineTime().call();
				}
				if(result && this.currentTime < this.deadlineTime){
					var deadlineTime = setInterval(()=>{
						this.currentTime++
						this.deadlineTimes = this.timeDown(this.deadlineTime,this.currentTime);
					},1000)
				}
			});
			
			// 获取名字
			this.$contract.getData('relationship').methods.AddressToNickName(this.$address).call((err,result)=>{
				this.name =  this.$web3.utils.hexToString(result)
			})
			
			try{
				// 订单总量
				var orderTotal = (await this.$contract.getData('controller').methods.QueryOrders(this.$address,0,255,0,10).call()).total;
				if(Number(orderTotal) == this.ProvideHelpList.length){return}
				this.ProvideHelpList = [];
				if(Number(orderTotal) <= 10){
					var maxOffset = 0;
				}else{
					var maxOffset = Number(orderTotal.substr(0,1)) * 10
				}
				// console.log(orderTotal,maxOffset)
				for(var k = maxOffset;k >= 0;k-=10){
					// 订单
					var QueryOrders = await this.$contract.getData('controller').methods.QueryOrders(this.$address,0,255,k,10).call()
					for(var key = (Number(QueryOrders.len) - 1); key >= 0 ; key--){
						var order = new this.$web3.eth.Contract(Order.abi,QueryOrders.orders[key]);
						var orderState = QueryOrders.states[key]
						if(orderState == '2' || orderState == '5'){
							var totalAmount = this.$fromWei(QueryOrders.totalAmounts[key],'','Mwei');
							
							// 最新块的时间
							var currentTime = await this.$currentTime();
							
							var pow64 = this.$BN(2).pow(this.$BN(64));
							var pow32 = this.$BN(2).pow(this.$BN(32));
							var OnCreated = this.$BN(QueryOrders.times[key]).div(pow64).toNumber();
							var OnCountDownStrat = this.$BN(QueryOrders.times[key]).mod(pow64).div(pow32).toNumber();
							var OnCountDownEnd = OnCountDownStrat + (3 * 86400);
							var CurrentProfitInfoTime = this.$BN(QueryOrders.times[key]).mod(pow32).toNumber()

							// 是否完成排队
							var CurrentProfitInfo = (await order.methods.CurrentProfitInfo().call())[0];
							
							if(currentTime > OnCountDownStrat && orderState == '2'){
								var OrderCountdownStarts = true;
							}else{
								var OrderCountdownStarts = false;
							}
							 
							this.ProvideHelpList.push({
								order: QueryOrders.orders[key],
								totalAmount: totalAmount,
								state: orderState,
								currentScale: orderState == '2' ? 50 : 100,
								cretaTime: OnCreated,
								timesDown: '00:00:00:00',
								OnCountDownEnd: OnCountDownEnd,
								CurrentProfitInfo: CurrentProfitInfoTime && currentTime > CurrentProfitInfoTime ? true : false,
								OrderCountdownStarts: OrderCountdownStarts,
							})
						}
					}
					this.times = setInterval(()=>{
						currentTime++;
						this.ProvideHelpList.forEach((item,index)=>{
							if(item.state == '2'){
								if(currentTime == 0 || currentTime > item.OnCountDownEnd){
									this.$set(this.ProvideHelpList[index],'timesDown','00:00:00:00')
									clearInterval(this.times)
									return 
								}else{
									var timesDown = this.timeDown(item.OnCountDownEnd,currentTime);
									this.$set(this.ProvideHelpList[index],'timesDown',timesDown)
								}
							}
						})
					},1000)
				}
			}catch(e){
				// console.log(e)
			}
		},
		timeDown(endDateStr,currentTime){
			function add(m){return m<10?'0'+m:m }
			//结束时间
			var endDate = endDateStr;
			//当前时间
			var nowDate = currentTime;
			//相差的总秒数
			var totalSeconds = parseInt((endDate - nowDate))
			//天数
			var day=parseInt(totalSeconds/3600/24);
			//取模（余数）
			var modulo = totalSeconds % (60 * 60 * 24);
				
			//小时数
			var hours = Math.floor((modulo / (60 * 60)));
			modulo = modulo % (60 * 60);
			//分钟
			var minutes = Math.floor(modulo / 60);
			//秒
			var seconds = modulo % 60;
			return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
		},
		WithdrawLuckAward(){
			this.$contract.getData('assertPoolAwardsI').methods.WithdrawLuckAward().send({
				from: this.$address,
			}).on('transactionHash' , txHash =>{
				console.log(txHash)
			}).on('error' , console.error)
			.on('receipt' , result=>{
				this.init()
				this.$router.replace('/drawCompensate')
			})
		},
		zanweikaifang(){
			Toast.fail(this.$t('NotYetOpen'))
		},
		onRefresh() {
		    setTimeout(() => {
		        this.init()
		        Toast('刷新成功');
		        this.isLoading = false;
		    }, 1000);
		},
	},
	mounted() {
		this.init()
		var mySwiper = new Swiper ('.swiper-container', {
		    direction: 'horizontal', // 垂直切换选项
		    //loop: true, // 循环模式选项
		    
		    // 如果需要分页器
		    pagination: {
		      el: '.swiper-pagination',
		    },
			slidesPerView : 3,
			spaceBetween: 10,
			
			autoHeight: true, //高度随内容变化
		})
	},
	destroyed() {
		clearInterval(this.times);
	}
}
</script>

