<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="exchangeBox">
			<div class="exchangeInputBox">
				<div class="exchangeTitle colorA1BDE1_1">{{$t('SwapPeriod')}}：{{roundID}}</div>
				<div class="exchangeProp colorA1BDE1">
					<span>1:{{prop}}</span>
					<span>{{Remaining}} DT</span>
				</div>
				<div class="exchangeSchedule">
					<div :style="'width:'+RemainingProp+'%'">
						<div>{{RemainingProp}}%</div>
					</div>
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Send')" v-model="value" @input="input" />
					<div class="colorA1BDE1">
						<img src="../../../assets/usdtLogo.png" />
						<span>USDT</span>
					</div>
				</div>
				<div class="exchangeBalance colorA1BDE1">
					{{$t('Available')}} {{usdt}} USDT
				</div>
				<div class="exchangeInput">
					<input type="number" :placeholder="$t('Received')" disabled="disabled" v-model="exchangeDT" />
					<div class="colorA1BDE1">
						<img src="../../../assets/dtLogo.png" />
						<span>DT</span>
					</div>
				</div>
				<!-- <div class="exchangeBalance colorA1BDE1">
					{{$t('RedeemableQuota')}} {{exchangeLimit}} DT
				</div> -->
				<button @click="exchange" v-ButClickLimit>{{$t('Commit')}}</button>
				
				<div class="exchangeWarning" @click="exchangeRules = true">兑换须知</div>
			</div>
			<div class="exchangeRecord colorA1BDE1_1">
				<div class="exchangeRecordTitle" @click="App_Push('/changeRecord')">
					<span>{{$t('SwapRecords')}}</span>
					<div>
						<span>{{$t('All')}}</span>
						<!-- <img v-lazy="require('../../../assets/next.png')"> -->
						<self-icon type="next"></self-icon>
					</div>
				</div>
				<div class="exchangeRecordBox">					
					<div class="exchangeRecordList" v-for="(item,index) in list" :key="index">
						<div>
							<span>{{$t('SwapSuccess')}}</span>
							<span>{{item.time}}</span>
						</div>
						<div>
							<div>
								<span>-{{item.inAmount}} USDT</span>
								<span>+{{item.outAmount}} DT</span>
							</div>
							<icon name='arrow'></icon>
						</div>
					</div>
					<!-- <not-record v-if="!list.length" :text="NoRecord"></not-record> -->
				</div>
			</div>
		</div>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='value' 
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
		
		<overlay :show="exchangeRules" class="exchangeRules" @click="exchangeRules = false">
			<div class="colorA1BDE1_1">
				<div class="title">兑换须知</div>
				<div class="content">
					<p>1.未参与提供帮助玩家每日兑换总额≤5USDT;</p>
					<p>2.已参与提供帮助玩家每日兑换总额≤100USDT;</p>
					<p>3.未兑换额度每日清零，次日08:00更新当日可兑换总额。</p>
				</div>
			</div>
		</overlay>
		
	</div>
</template>

<script>
	import { Overlay, Button , Icon , Toast } from 'vant'
	import Authorization from '@/components/Authorization.vue'
	import notRecord from '@/components/not-record'
	import selfIcon from '@/components/self-icon'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { Overlay, Button , Icon , Authorization , notRecord , selfIcon , navBar },
		data() {
			return {
				// show: false
				value: '',
				exchangeDT: '',
				info: '',
				prop: '',
				usdt: 0,
				total: '',
				current: '',
				RemainingProp: 0,
				authorizedAccess: false,
				authorizationEnds: false,
				list: [],
				Remaining: 0,
				roundID: '',
				NoRecord: this.$t('NoRecord'),
				exchangeLimit: 0,
				title: this.$t('swap'),
				exchangeRules: false,
			}
		},
		methods:{
			async init(){
				
				this.authorizedAccess = false;
				
				// usdt
				this.$contract.getData('usdtToken').methods.balanceOf(this.$address).call((err,result)=>{
					if(result){
						this.usdt = this.$fromWei(result,'','Mwei');
					}
				})
				
				this.info = await this.$contract.getData('swapState').methods.swapInfo().call();
				if(Number(this.info.roundID) < 10){
					this.roundID = "00"+this.info.roundID
				}else if(Number(this.info.roundID) < 100){
					this.roundID = "0"+this.info.roundID
				}else{
					this.roundID = this.info.roundID
				}
				this.prop = this.$fromWei(this.$BN(this.info.prop).mul(this.$BN(this.$toWei('1','Mwei'))).toString())
				this.total = this.$fromWei(this.info.total)
				this.current = this.$fromWei(this.info.current)
				this.Remaining = this.$BN(this.total).sub(this.$BN(this.current)).toNumber()
				this.RemainingProp = this.$BN(this.Remaining * 100).div(this.$BN(this.total)).toNumber()
				
				//兑换上限
				this.exchangeLimit = this.$fromWei(await this.$contract.getData('swapLimit').methods.exchangeLimit(this.$address).call(),'','Mwei');
				this.exchangeLimit *= this.prop
				
				// console.log(this.RemainingProp)
			},
			input(){
				if(this.value == '' || this.value == 0){
					this.exchangeDT = ''
					return 
				}
				var value = this.$toWei(this.value.toString(),'Mwei');
				this.exchangeDT = this.$fromWei(this.$BN(value).mul(this.$BN(this.info.prop)).toString());
			},
			async exchange(){
				var value = Number(this.value)
				if(value == '' || value == 0){
					return Toast.fail(this.$t('PleaseEnterTheTransferAmount'))
				}else if(value > this.usdt){
					return Toast.fail(this.$t('InsufficientBalance'))
				}else if(this.exchangeDT < 1){
					return Toast.fail(this.$t('LessThanTheMinimumLimit'))
				}else if(this.exchangeDT > this.exchangeLimit){
					return Toast.fail(this.$t('RedeemableLimitExceeded'))
				}else if(this.exchangeDT > this.Remaining){
					return Toast.fail(this.$t('ExcessRemaining'))
				}
				
				// console.log(this.$contract.getData('swap')._address)
				var code = (await this.$contract.getData('swap').methods.Doswaping(this.$toWei(this.value.toString(),'Mwei')).call({from:this.$address})).code;
				if(code == '1'){
					return this.authorizedAccess = true;
				}
				this.$contract.getData('swapI').methods.Doswaping(this.$toWei(this.value.toString(),'Mwei')).send({
					from : this.$address,
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					// this.init()
					// this.value = '';
					// this.exchangeDT = '';
					window.location.reload()
				})
			},
			authorizedAccessFun(value){
				this.$contract.getData('usdtTokenI').methods.approve(this.$contract.getData('swap')._address,this.$web3.utils.toWei(value.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					// this.init()
					this.authorizedAccess = false;
					this.authorizationEnds = true;
					this.exchange()
				})
			},
			end(){
				this.authorizedAccess = false;
			},
		},
		async mounted() {
			this.init()
			
			var fromBlock = (await this.$web3.eth.getBlock('latest').then()).number;
			if(fromBlock - 7200 < 0){
				fromBlock = 0
			}else{
				fromBlock -= 7200
			}
			var address = this.$address
			this.$contract.getData('swapWs').events.Log_Swaped({
				fromBlock: fromBlock,
				filter: {owner: address}
			}, (error, event)=>{ })
			.on('data', async event => {	
				this.list.unshift({
					inAmount: this.$fromWei(event.returnValues.inAmount,'','Mwei'),
					outAmount: this.$fromWei(event.returnValues.outAmount),
					time: this.$toTime(event.returnValues.time,'Y-M-D H:i'),
				})
			})
			.on('changed', function(event){
			})
			.on('error', console.error);
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
