		padding: 20px 30px 20px;
		height: calc(100vh - 120px - 60px);
		// display: flex;
		// flex-direction: column;
	}
	.exchangeInputBox{
		width: 100%;
		// height: 722px;
		padding: 20px 44px 22px;
		// background-color: #273446;
		// box-shadow: 0 6px 4px #000000;
		border-radius: 8px;
		
		.exchangeTitle{
			font-size: 36px;
			// color: #A1BDE1;
			margin-bottom: 10px;
			padding-bottom: 10px;
			// border-bottom: 2px solid #18222F;
		}
		
		.exchangeProp{
			font-size: 32px;
			display: flex;
			justify-content: space-between;
			// color: #A1BDE1;
		}
		
		.exchangeSchedule{
			width: 100%;
			height: 36px;
			border-radius: 10px;
			// background-color: #516A89;
			position: relative;
			margin: 10px 0 36px;
			
			&>div{
				width: 0%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background: linear-gradient(134deg,rgba(33,193,122,1) 0%,rgba(33,193,168,1) 100%);
				border-radius: 10px;
				
				&>div{
					position: absolute;
					color: #fff;
					font-size: 24px;
					left: 10px;
					top: 50%;
					transform: translateY(-50%);
					text-align: left;
				}
			}
		}
		
		.exchangeInput{
			display: flex;
