			border-radius: 10px;
			
			&>input{
				width: 381px;
				height: 112px;
				background-color: transparent;
				border: none;
				padding-left: 30px;
			}
			
			&>div{
				flex: 1;
				height: 108px;
				// color: #A1BDE1;
				// border-left: 2px solid #4d6784;
				display: flex;
				// justify-content: center;
				align-items: center;
				padding-left: 20px;
				
				&>img{
					width: 43px;
					margin-right: 34px;
				}
			}
		}
		
		.exchangeBalance{
			// color: #A1BDE1;
			font-size: 30px;
			text-align: left;
			margin: 14px 0;
		}
		
		&>button{
			width: 400px;
			height: 96px;
			border-radius: 10px;
