			font-size: 40px;
			color: #fff;
			border: none;
			margin-top: 30px;
		}
		
		.exchangeWarning{
			font-size: 26px;
			margin-top: 20px;
		}
	}
	
	.exchangeRecord{
		width: 100%;
		// background-color: #273446;
		margin-top: 26px;
		display: flex;
		flex-direction: column;
		// color: #A1BDE1;
		border-radius: 8px;
		// flex: 1;
		// height: 500px;
		position: relative;
		
		&>.exchangeRecordTitle{
			width: 100%;
			height: 100px;
			padding: 0 18px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 32px;
			// border-bottom: #17212E 2px solid;
			
			&>div{
				img{
					width: 13px;
					height: 25px;
					margin-left: 12px;
				}
				
				&>svg{
					font-size: 28px;
				}
			}
		}
		
		&>.exchangeRecordBox{
			width: 100%;
			flex: 1;
			height: calc(750px - 100px);
			padding: 0 24px;
			overflow-y: scroll;
			
			&>.exchangeRecordList{
				width: 100%;
				height: 98px;
				border-bottom: #17212E 2px solid;
				font-size: 26px;
				display: flex;
				justify-content: space-between;
