<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox">
			<div v-for="(item, index) in list" :key="index">
				<div>
					<span>{{ item.name }}</span>
					<span>{{ item.type }}</span>
				</div>
				<div>
					<span>{{ item.date }}</span>
					<span>+{{ item.amount }} USDT</span>
				</div>
			</div>
			<not-record v-if="!list.length" :text="NoRevenueRecord"></not-record>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import notRecord from '@/components/not-record'
	
	export default {
		components: { navBar, notRecord },
		data() {
			return {
				title: this.$t('IncomeDetails'),
				list: [
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' },
					// { name: '小鹏', type: '管理奖', date: '2019/7/16', amount: '0.5500' }
				],
				NoRevenueRecord: this.$t('NoRevenueRecord')
			}
		},
		async mounted() {
			var getBlockNumber = await this.$web3.eth.getBlockNumber().then()
			if(getBlockNumber < 100000){
				getBlockNumber = 0
			}else{
				getBlockNumber -= 100000
			}
			this.$contract.getData('rewardWs').events.Log_Award({
				fromBlock: getBlockNumber,
				filter: { owner:this.$address }
			}, function(error, event){ })
			.on('data', async event => {
				// console.log(event);
				var address = event.returnValues.owner;
				if(Number(event.returnValues.atype) == 0){
					var typeText = this.$t('expand')
				}else if(Number(event.returnValues.atype) == 1){
					var typeText = this.$t('Executives')
				}else if(Number(event.returnValues.atype) == 2){
					var typeText = this.$t('manager')
				}else if(Number(event.returnValues.atype) == 3){
					var typeText = this.$t('culture')
				}
				this.list.unshift({
					amount: this.$fromWei(event.returnValues.amount,'','Mwei'),
					date: this.$toTime(event.returnValues.time,'Y-M-D H:i'),
					type: typeText,
					name: this.$web3.utils.hexToString(await this.$contract.getData('relationship').methods.AddressToNickName(this.$address).call()),
					time: event.returnValues.time,
				})
				this.list.sort(function(a,b){
					return b.time - a.time
				})
			})
			.on('changed', function(event){
			})
			.on('error', console.error);
			
			// axios.get('https://api-cn.etherscan.com/api',{
			// 	params: {
			// 		"module": 'logs',
			// 		"action": 'getLogs',
			// 		"fromBlock": 0,
			// 		"toBlock": 'latest',
			// 		"address": this.$contract.getData('reward')._address,
			// 		"topic0": this.$web3.utils.sha3('Log_Award(address,uint8,uint256,uint256)'),
			// 		"topic1": this.$address,
			// 		"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU'
			// 	}
			// }).then(res => {
				
			// })
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		// color: #A1BDE1;
		font-size: 28px;
		position: relative;
		padding: 20px 40px 0;
		// background-color: #273446;
		&>div {
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #516A89;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				&:first-child {
					margin-bottom: 20px;
				}
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}
</style>
