class ShowWallet {
	constructor() {
		this.show = true
	}
	
	set(arg) {
		this.show = arg
	}
}

Vue.prototype.$web3 = $web3
Vue.prototype.IWeb3 = IWeb3
Vue.prototype.WsWeb3 = WsWeb3
Vue.prototype.ETHWeb3 = ETHWeb3
