Vue.prototype.$currentTime = currentTime
Vue.prototype.$cache = {}
Vue.prototype.$guide = guide
Vue.prototype.$showWallet = new ShowWallet()
Vue.prototype.App_Push = (url,key,value,)=>{
	 if (window.xwNavigatorPush) {		 
		if(url.indexOf('https') === 0){
			return xwNavigatorPush.postMessage(url)
		}else if(key){
			return xwNavigatorPush.postMessage(window.location.origin+'/#'+url+'?'+key+'='+value)
		}
		xwNavigatorPush.postMessage(window.location.origin+'/#'+url)
	 }else{
		if(url.indexOf('https') === 0){
		 	return router.push(url);
		 }else if(key){
		 	return router.push({
				'path': url,
				'query':{order:value},
			})
		 }
	 }
}
Vue.prototype.App_Pop = ()=>{
	if(window.xwNavigatorPop){
		xwNavigatorPop.postMessage('1')
	}else{
		router.back()
	}
}
Vue.directive('ButClickLimit',{
	inserted: function(el){
		el.addEventListener('click',()=>{
			el.disabled = true;
			setTimeout(()=>{
				el.disabled = false;
			},2000)
		})
	}
})

initAddress(() => {
	new Vue({
		router,
		i18n,
		render: h => h(App)
	}).$mount('#app')
})

