<template>
	<div class="box">
		<!-- <nav-bar :title='title'></nav-bar> -->
		<div class="navBox orderDetailsBoxBox">
			<div class="orderDetailsBox">
				<div class="orderDetailsBoxTop">
					<div class="orderDetailsBoxTopCircle" v-if="list.IsBreaker === false">
						<icon name="success" class="active"></icon>
						<div :style="'width:'+(isComplete === false ? 50 : 100)+'%'" :class="{yellowBackground:list.orderState == '5',greenBackground:list.orderState == '2' || list.orderState == '7' || list.orderState == '6'}"></div>
						<div :class="{yellowBorder:list.orderState == '5',greenBorder:list.orderState == '2' || list.orderState == '6' || list.orderState == '7'}"></div>
					</div>
					<div class="orderDetailsBoxTopCircle" v-if="list.IsBreaker == true">
						<icon name="cross" class="active"></icon>
						<div :style="'width: 100%'" style="background-color: #EB4948;"></div>
						<div style="border:2px solid #EB4948;" ></div>
					</div>
					<!-- <span>{{list.OnCreatedToTime}}</span> -->
				</div>
				<div class="orderDetailsBoxSchedule">
					<div class="orderDetailsBoxScheduleContent" :class="{red:list.IsBreaker}">
						<div class="orderDetailsBoxScheduleActiveLine" v-if="!list.IsBreaker" :style="'width:'+list.currentScale+'%'" ></div>
						<div class="orderDetailsBoxScheduleActiveLine" v-else-if="list.IsBreaker" :style="'width:100%'"></div>
						<div class="circleAnimation" :style="'left:'+list.currentScale+'%'" v-if="!list.IsBreaker">
							<div class="one"></div>
							<div class="two"></div>
							<div class="three" v-if="list.orderState == '2'">{{$t('ProvideHelp')}}({{list.currentScale}}%)</div>
							<div class="three" v-else-if="list.orderState == '5' && list.income == false">{{$t('Earning')}}</div>
							<div class="three" v-else-if="list.orderState == '5' && list.income == true">{{$t('RevenueCompletion')}}</div>
							<div class="three" v-else-if="list.orderState == '6'">{{$t('GetHelp01')}}({{list.currentScale}}%)</div>
						</div>
						<div class="orderDetailsBoxScheduleStrat"><span>{{$t('Create')}}</span></div>
						<!-- <div class="orderDetailsBoxSchedulePay"><span>正在排队</span></div>
						<div class="orderDetailsBoxScheduleQueue"><span>收益</span></div>
						<div class="orderDetailsBoxScheduleIncome"><span>收益完成</span></div> -->
						<div class="orderDetailsBoxScheduleEnd" v-if="!list.IsBreaker" :style="{ justifyContent: $i18n.locale == 'zh' ? 'center' : 'flex-end' }"><span>{{$t('Complete')}}</span></div>
						<div class="orderDetailsBoxScheduleEnd" v-else-if="list.IsBreaker"><span>{{$t('Default')}}</span></div>
					</div>
					<div class="orderDetailsBoxCreateTime">
						<span>{{$toTime(list.OnCreated,'Y/M/D H:i')}}</span>
						<span v-if="list.orderState == '7'">{{$toTime(list.succesOrderTime,'Y/M/D H:i')}}</span>
					</div>
				</div>
				<div class="orderDetailsBoxState01" v-if="list.orderState == '2' && list.IsBreaker != true">
					<div v-if="list.OnCountDownState">
						<div>{{list.times.substr(0,1)}}</div>
						<div>{{list.times.substr(1,1)}}</div>
						<p>:</p>
						<div>{{list.times.substr(3,1)}}</div>
						<div>{{list.times.substr(4,1)}}</div>
						<p>:</p>
						<div>{{list.times.substr(6,1)}}</div>
						<div>{{list.times.substr(7,1)}}</div>
					</div>
					<Button @click="transferUSDT" v-ButClickLimit>{{$t('Payment')}}</Button>
					<span @click="rule = true;ruleType = 0">{{$t('PaymentRules')}}</span>
				</div>
				<div class="orderDetailsBoxState02" v-else-if="list.orderState == '5' && list.income == false">{{$t('TimeToCompletion')}} {{list.queueDay}}</div>
				<div class="orderDetailsBoxState03" v-else-if="list.orderState == '5' && list.income == true">
					<div>
						<div>{{list.timeNumberText.substr(0,1)}}</div>
						<div>{{list.timeNumberText.substr(1,1)}}</div>
						<p>:</p>
						<div>{{list.timeNumberText.substr(3,1)}}</div>
						<div>{{list.timeNumberText.substr(4,1)}}</div>
						<p>:</p>
						<div>{{list.timeNumberText.substr(6,1)}}</div>
						<div>{{list.timeNumberText.substr(7,1)}}</div>
					</div>
					<Button @click="pushProvideHelp = true" :disabled="!list.timeNumberState">{{$t('GetHelp01')}}</Button>
					<span @click="rule = true;ruleType = 1">{{$t('GetHelpRules')}}</span>
				</div>
				<div class="orderDetailsBoxState04" v-else-if="list.orderState == '6' && (list.CrashTime >= 24 && list.CrashTimeResult)">
					<Button @click="ApplyProfitingCountDown" v-ButClickLimit>{{$t('ApplicationCountdown')}}</Button>
					<span @click="rule = true;ruleType = 2">{{$t('AccelerationRule')}}</span>
				</div>
				<!-- <div class="orderDetailsBoxState04" v-else-if="list.orderState == '6'">
					<span>已开启支付倒计时</span>
				</div> -->
			</div>
			<div class="orderDetailsDataInfo">
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate01" :rate="list.currentPaymentedAmountProp" :speed="100" :text="list.currentPaymentedAmountString" :stroke-width="120" color='#6E5EFF' ></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.totalAmount}}</span>
					<span>{{$t('ProvideHelp')}}</span>
				</div>
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate02" :rate="list.currentIncome" :speed="100" :text="list.totalIncome" :stroke-width="120" color='#FF9A52' ></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.incomeTotal}}</span>
					<span>{{$t('expectedProfits')}}</span>
				</div>
				<div>
					<div class="circleBgbox">
						<vant-circle v-model="currentRate03" :rate="list.getHelpProp" :speed="100" :text="this.list.getHelpedAmountString" :stroke-width="120" color='#22CED4'></vant-circle>
						<div class="circleBg"></div>
					</div>
					<span>{{list.getHelpedAmountTotalString}}</span>
					<span>{{$t('GetHelp01')}}</span>
				</div>
			</div>
			<div class="orderDetailsTranctionRecord">
				<div class="myContractBoxTitle">
					<span>{{$t('recording')}}</span>
				</div>
				<div class="myContractBoxList">
					<!-- <div>
						<img src="../../../assets/Log_HelpTo.png" />
						<div class="myContractBoxListRight">
							<div>
								<span>Sofia</span>
								<span>0x000000000...0000000000</span>
							</div>
							<div>
								<span class="green">-500.00 USDT</span>
								<span>2020/04/20  02:27</span>
							</div>
							<img src="../../../assets/next.png" />
						</div>
					</div> -->
					<not-record v-if="!Record.length" :text="NoTransactionHistory"></not-record>
					<!-- <div v-for="(item,index) in Record" :key="index" @click="$route.query.order = item.order;init()"> -->
					<div v-for="(item,index) in Record" :key="index">
						<img src="../../../assets/Log_HelpGet.png" v-if="item.type == 'Get'" />
						<img src="../../../assets/Log_HelpTo.png" v-else />
						<div class="myContractBoxListRight">
							<div>
								<span>{{item.name}}</span>
								<span>{{item.address.substr(0,8)}}......{{item.address.substr(-8,8)}}</span>
							</div>
							<div>
								<span :class="{red:item.type == 'To',green:item.type == 'Get'}">{{item.type == 'Get' ? '+' : '-'}}{{$fromWei(item.amount,'','Mwei')}} USDT</span>
								<!-- <span>{{$toTime(item.time,'Y/M/D H:i')}}</span> -->
							</div>
							<!-- <img src="../../../assets/next.png" /> -->
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='list.RemainingAmount' 
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
		
		<overlay :show="pushProvideHelp" class="pushProvideHelp" @click="pushProvideHelp = false">
			<div @click.stop>
				<div>
					<span>{{$t('GetHelp01')}}</span>
					<span>{{Number(list.totalAmount) + Number(list.totalIncome)}} USDT</span>
				</div>
				<div>
					<span>{{$t('RelatedOrder')}}</span>
					<span @click="App_Push('/provideHelp')" v-if="list.RelatedOrder === false">{{$t('ToFinish')}}</span>
					<span v-else>{{$t('Completed')}}</span>
				</div>
				<div>
					<span>{{$t('FuelConsumption')}}</span>
					<span>{{list.ConsumeDT}} DT</span>
				</div>
				<span>{{$t('Available')}}：{{this.list.DT}} DT</span>
				<Button @click="DoConvertToConsumer" :disabled="list.RelatedOrder === false" v-ButClickLimit>{{$t('Commit')}}</Button>
			</div>
		</overlay>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div>
				<div class="ruleTop">					
					<div>
						<icon name="fail"></icon>
					</div>
					<span v-if="ruleType == 0">{{$t('PaymentRules')}}</span>
					<span v-else-if="ruleType == 1">{{$t('GetHelpRules')}}</span>
					<span v-else-if="ruleType == 2">{{$t('AccelerationRule')}}</span>
				</div>
				<div class="ruleBottom" v-if="ruleType == 0">
					<p class="marginBottom">{{$t('PaymentRulesText01')}}</p>
					<p>{{$t('PaymentRulesText02')}}</p>
					<p>{{$t('PaymentRulesText03')}}</p>
					<p>{{$t('PaymentRulesText04')}}</p>
					<p class="marginBottom">{{$t('PaymentRulesText05')}}</p>
					<p class="marginBottom">{{$t('PaymentRulesText06')}}</p>
					<p>{{$t('PaymentRulesText07')}}</p>
					<div></div>
				</div>
				<div class="ruleBottom" v-else-if="ruleType == 1">
					<p>{{$t('GetHelpRulesText01')}}</p>
					<p>{{$t('GetHelpRulesText02')}}</p>
					<p>{{$t('GetHelpRulesText03')}}</p>
					<p>{{$t('GetHelpRulesText04')}}。</p>
					<div></div>	
				</div>
				<div class="ruleBottom" v-else-if="ruleType == 2">
					<p>{{$t('AccelerationRuleText01')}}</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Rate , Button , Circle as vantCircle , Toast , Overlay } from 'vant';
	import navBar from '@/components/nav-bar.vue';
	// import goldBag from "@/components/gold-bag.vue"
	// import hourglass from "@/components/hourglass.vue"
	// import Swiper from 'swiper'
	// import 'swiper/css/swiper.min.css'
	// import 'swiper/js/swiper.min.js'
	// import QRCode from 'qrcode'
	import Order from "../../../static/Order.json"
	import Authorization from '@/components/Authorization.vue'
	import notRecord from '@/components/not-record'
	export default {
		components:{ navBar , Icon , Rate , Button , vantCircle , Authorization , Overlay , notRecord },
		data () {
			return{
				title: '',
				NoTransactionHistory: this.$t('NoTransactionHistory'),
				order: '',
				currentRate01: 0,
				currentRate02: 0,
				currentRate03: 0,
				list: {
					OnCreated: '',
					order: '',
					RelatedOrder: '',
					currentIncome: 0,
					getHelpProp: 0,
					getHelpedAmountString: '0',
					totalIncome: '0',
					currentScale: 0,
					times: '00:00:00:00',
					IsBreaker: '',
					getHelpedAmountTotal: 0,
					currentPaymentedAmountProp: 0,
					getHelpedAmountTotalString: 0,
					totalAmount: 0,
					incomeTotal: 0,
					currentPaymentedAmountString: '0',
					succesOrderTime: 0,
					timeNumberText: '00:00:00:00',
					timeNumberState: false,
				},
				Record: [],
				times: '',
				authorizedAccess: false,
				authorizationEnds: false,
				pushProvideHelp: false,
				rule: false,
				ruleType: 0,
				isComplete: '',
			}
		},
		methods:{
			async init(callback) {
				clearInterval(this.times);
				this.$set(this.list,'currentScale',0)
				this.pushProvideHelp = false
				
				var order = new this.$web3.eth.Contract(Order.abi,this.$route.query.order);
				try{
					var isLink = await order.methods.isLink().call()
				}catch(e){
					var isLink = true;
					var linkedOrderAddress = true;
				}
				if(isLink){
					if(linkedOrderAddress){
						order = new this.ETHWeb3.eth.Contract(Order.abi, this.$route.query.order);
					}else{						
						const ethContractAddress = await order.methods.linkedOrderAddress().call()
						order = new this.ETHWeb3.eth.Contract(Order.abi, ethContractAddress);
					}
					this.orderWs = order;
					this.order = order;
				}else{
					this.orderWs = new this.WsWeb3.eth.Contract(Order.abi,this.$route.query.order);
					this.order = new this.IWeb3.eth.Contract(Order.abi,this.$route.query.order);
				}
				callback ? callback() : ''
				// 合约地址
				this.list.order = this.$route.query.order;
				// 合约归属人
				this.$set(this.list,'contractOwner',await order.methods.contractOwner().call())
				if(this.list.contractOwner == this.$address){
					this.title = this.$t('MyContract')
				}else{
					var name = this.$web3.utils.hexToString(await this.$contract.getData('relationship').methods.AddressToNickName(this.list.contractOwner).call())
					this.title = name+this.$t('contract')
				}
				if(window.xwNavigatorSetTitle){
					xwNavigatorSetTitle.postMessage(this.title);
				}
				
				// 状态
				this.$set(this.list,'orderState',await order.methods.orderState().call())
				// 订单类型
				this.$set(this.list,'orderType',await order.methods.orderType().call())
				// 订单号
				this.$set(this.list,'orderIndex',await order.methods.orderIndex().call())
				// usdt授权额度
				this.$set(this.list,'USDTAuthorizedAccess',this.$fromWei(await this.$contract.getData('usdtToken').methods.allowance(this.$address,order._address).call({from:this.$address}),'','Mwei'))
				// order的USDT数量
				this.$set(this.list,'usdt',this.$fromWei(await this.$contract.getData('usdtToken').methods.balanceOf(this.$route.query.order).call(),'','Mwei'))
				// 创建时间
				this.$set(this.list,'OnCreated',Number(await order.methods.times(0).call()))
				this.$set(this.list,'OnCreatedToTime',this.$toTime(this.list.OnCreated,'Y/M/D H:i'))
				// 倒计时开始时间
				this.$set(this.list,'OnCountDownStart',Number(await order.methods.times(4).call()))
				// 倒计时结束时间
				this.$set(this.list,'OnCountDownEnd',Number(await order.methods.times(5).call()))
				// 完成订单时间
				this.$set(this.list,'succesOrderTime',Number(await order.methods.times(8).call()))
				// 最新块号时间
				var currentTime = await this.$currentTime()
				// 订单倒计时是否开启
				this.$set(this.list,'OnCountDownState',currentTime > Number(this.list.OnCountDownStart) ? true : false)
				// 是否违约
				this.$set(this.list,'IsBreaker',Number(await order.methods.OrderStateCheck().call()) == 4 ? true : false)
				
				if(this.list.orderType == 2){
					// 可获得帮助总量
					this.$set(this.list,'getHelpedAmountTotal',Number(await order.methods.getHelpedAmountTotal().call()))
					this.$set(this.list,'getHelpedAmountTotalString',this.$fromWei(await order.methods.getHelpedAmountTotal().call(),'','Mwei'))
					
					// 当前获得帮助
					var getHelpedAmount = Number(await order.methods.getHelpedAmount().call())
					this.$set(this.list,'getHelpedAmountString',this.$fromWei(getHelpedAmount.toString(),'','Mwei').toString())
					
					// 当前获得帮助占比
					this.$set(this.list,'getHelpProp',this.$BN(getHelpedAmount * 100).div(this.$BN(this.list.getHelpedAmountTotal)).toNumber())
					this.$set(this.list,'currentScale',this.$BN(getHelpedAmount * 100).div(this.$BN(this.list.getHelpedAmountTotal)).toNumber())
					// 消耗燃料
					var WithdrawCostProp = await this.$contract.getData('config').methods.WithdrawCostProp().call();
					var USDTToDTProp = await this.$contract.getData('config').methods.USDTToDTProp().call();
					// (CurrentP1 , CurrentP2) * WithdrawCostProp / this.$toWei('1','szabo') * USDTToDTProp
					this.$set(this.list,'ConsumeDT',this.$fromWei(this.$BN(this.$toWei(this.list.getHelpedAmountTotalString.toString(),'Mwei')).mul(this.$BN(WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(USDTToDTProp)).toString()))
					
					// 崩盘时间
					if(this.list.orderState == '6'){
						// 转换消费者的时间
						var OnConvertConsumer = Number(await order.methods.times(6).call())
						
						this.$set(this.list,'CrashTime',Math.floor((currentTime - OnConvertConsumer) / 3600))
						var ApplyProfitingCountDown = await this.order.methods.ApplyProfitingCountDown().call({from:this.$address});
						this.$set(this.list,'CrashTimeResult',ApplyProfitingCountDown.canApply && ApplyProfitingCountDown.applyResult ? true : false)
						// console.log(ApplyProfitingCountDown)
					}
					return 
				}
				
				// 总量
				this.$set(this.list,'totalAmount',this.$fromWei(await order.methods.totalAmount().call(),'','Mwei'))
				// 当前系统已经支付了多少
				var toHelpedAmount = this.$fromWei(await order.methods.toHelpedAmount().call(),'','Mwei');
				// 已付款
				this.$set(this.list,'currentPaymentedAmount',this.$BN(toHelpedAmount).add(this.$BN(this.list.usdt)))
				this.$set(this.list,'currentPaymentedAmountString',this.list.currentPaymentedAmount.toString())
				// 还需要付款多少
				this.$set(this.list,'RemainingAmount',this.$BN(this.list.totalAmount).sub(this.$BN(this.list.currentPaymentedAmount)).toString())
				// 已付款的百分比
				this.$set(this.list,'currentPaymentedAmountProp',this.$BN(this.list.currentPaymentedAmount*100).div(this.$BN(this.list.totalAmount)).toNumber())
				
				// 是否完成订单
				if(this.list.orderState == '2' || this.list.orderState == '4' || this.list.orderState == '6'){
					this.isComplete = false
				}else{
					this.isComplete = true
				}
				
				if(this.list.orderState != '2'){
					// 计算预期收益
					var ProfitPropP1 = this.$fromWei(await this.$contract.getData('config').methods.ProfitPropP1().call(),'','szabo');
					var ProfitPropTotalP2 = this.$fromWei(await this.$contract.getData('config').methods.ProfitPropTotalP2().call(),'','szabo');
					var p1 = this.$BN(this.list.totalAmount).mul(this.$BN(((this.list.OnCountDownStart - this.list.OnCreated) / 3600 / 24))).mul(this.$BN(ProfitPropP1*100)).toNumber()/100;
					var p2 = this.$BN(this.list.totalAmount).mul(this.$BN(ProfitPropTotalP2 * 100)).toNumber() / 100
					
					// 预期收益倍数
					var paymentPartMinLimit = Number(await this.orderWs.methods.paymentPartMinLimit().call());
					if(paymentPartMinLimit <= 10 && paymentPartMinLimit >= 1){
						this.$set(this.list,'incomeTotal',(p1+p2) / paymentPartMinLimit)
					}else{
						this.$set(this.list,'incomeTotal',(p1+p2))
					}
					
					
					//p1阶段时间
					// var WaitTime = Number(await this.$contract.getData('config').methods.WaitTime().call())
					//p2阶段最小时间
					var ForzenTimesMin = Number(await this.$contract.getData('config').methods.ForzenTimesMin().call())
					//p2阶段最大时间
					var ForzenTimesMax = Number(await this.$contract.getData('config').methods.ForzenTimesMax().call())
					//p2实际时间
					var p2Time = ForzenTimesMin + (Number(this.list.orderIndex) * 86400) > ForzenTimesMax ? ForzenTimesMax : ForzenTimesMin + (Number(this.list.orderIndex) * 86400)
					
					// 订单排队时间
					this.$set(this.list,'queueDay',this.$toTime(this.list.OnCountDownStart+p2Time,'Y/M/D H:i'))
					
					// 当前收益
					this.$set(this.list,'CurrentProfitInfo',await order.methods.CurrentProfitInfo().call())
					var CurrentP1 = this.$fromWei(this.list.CurrentProfitInfo[1],'','Mwei')
					var CurrentP2 = this.$fromWei(this.list.CurrentProfitInfo[2],'','Mwei')
					// 所有收益(p1 + p2)
					this.$set(this.list,'totalIncome',(CurrentP1+CurrentP2).toString())
					// 当前收益占比(预期收益 / 当前收益)
					this.$set(this.list,'currentIncome',this.$BN((CurrentP1+CurrentP2)*100).div(this.$BN(p1 + p2)).toNumber())
					// console.log(CurrentP2)
					// 提供帮助后状态判断
					if(!CurrentP2){
						this.$set(this.list,'currentScale',50)
						this.$set(this.list,'income',false)
					}else{
						this.$set(this.list,'income',true)
						if(this.list.orderState == '7' || this.list.orderState == '6'){
							// 可获得帮助总量
							this.$set(this.list,'getHelpedAmountTotal',Number(await order.methods.getHelpedAmountTotal().call()))
							this.$set(this.list,'getHelpedAmountTotalString',this.$fromWei(this.list.getHelpedAmountTotal.toString(),'','Mwei').toString())
							
							// 当前获得帮助
							var getHelpedAmount = Number(await order.methods.getHelpedAmount().call())
							this.$set(this.list,'getHelpedAmountString',this.$fromWei(getHelpedAmount.toString(),'','Mwei').toString())
							
							// 当前获得帮助占比
							this.$set(this.list,'getHelpProp',this.$BN(getHelpedAmount * 100).div(this.$BN(this.list.getHelpedAmountTotal)).toNumber())
							this.$set(this.list,'currentScale',this.$BN(getHelpedAmount * 100).div(this.$BN(this.list.getHelpedAmountTotal)).toNumber())
							
							// 崩盘时间
							if(this.list.orderState == '6'){
								// 转换消费者的时间
								var OnConvertConsumer = Number(await order.methods.times(6).call())
								
								this.$set(this.list,'CrashTime',Math.floor((currentTime - OnConvertConsumer) / 3600))
								var ApplyProfitingCountDown = await this.order.methods.ApplyProfitingCountDown().call({from:this.$address});
								this.$set(this.list,'CrashTimeResult',ApplyProfitingCountDown.canApply && ApplyProfitingCountDown.applyResult ? true : false)
								// console.log(ApplyProfitingCountDown)
							}
						}else{
							if(this.list.CurrentProfitInfo[0] == '1' || this.list.CurrentProfitInfo[0] == '4'){
								this.$set(this.list,'currentScale',100)
								this.$set(this.list,'income',true)
								
								const OnUnfreezing = Number(await order.methods.times(7).call());
								const countdownStart = 1604982762;
								if(OnUnfreezing < countdownStart){
									var timeNumber = countdownStart + (36 * 3600)
								}else{
									var timeNumber = OnUnfreezing + (36 * 3600)
								}
								var timeNumbers = setInterval(()=>{
									if(currentTime > timeNumber){
										this.$set(this.list,'timeNumberState',false)
										this.$set(this.list,'timeNumberText','00:00:00:00')
										clearInterval(this.times)
									}else{
										this.$set(this.list,'timeNumberState',true)
										var timesDown = this.timeDown(timeNumber,currentTime);
										this.$set(this.list,'timeNumberText',timesDown)
									}
									currentTime++;
								},1000)
								
								this.$set(this.list,'DT',this.$fromWei(await this.IWeb3.eth.getBalance(this.$address).then()))
								var WithdrawCostProp = await this.$contract.getData('config').methods.WithdrawCostProp().call();
								var USDTToDTProp = await this.$contract.getData('config').methods.USDTToDTProp().call();
								// (CurrentP1 , CurrentP2) * WithdrawCostProp / this.$toWei('1','szabo') * USDTToDTProp
								this.$set(this.list,'ConsumeDT',this.$fromWei(this.$BN(this.$toWei((CurrentP1 + CurrentP2 + this.list.totalAmount).toString(),'Mwei')).mul(this.$BN(WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(USDTToDTProp)).toString()))
								
								const can = await this.$contract.getData('orderRelationsPlugins').methods.canConvertConsumer(this.$address).call()
								this.$set(this.list,'RelatedOrder',can)
								// if(this.list.CurrentProfitInfo[0] == '4'){
								// 	this.$set(this.list,'RelatedOrder',false)
								// }else{
								// 	this.$set(this.list,'RelatedOrder',true)
								// }
							}else if(this.list.CurrentProfitInfo[0] == '3'){
								this.$set(this.list,'currentScale',50)
								this.$set(this.list,'income',false)
							}
						}
					}
				}else if(this.list.orderState == '2'){
					// 已付款的百分比
					this.$set(this.list,'currentScale',this.$BN(this.list.currentPaymentedAmount*100).div(this.$BN(this.list.totalAmount)).toNumber())
					
					this.times = setInterval(()=>{
						currentTime++;
						if(currentTime == 0 || currentTime > this.list.OnCountDownEnd){
							this.$set(this.list,'times','00:00:00:00')
							clearInterval(this.times)
							return 
						}else{
							var timesDown = this.timeDown(this.list.OnCountDownEnd,currentTime);
							this.$set(this.list,'times',timesDown)
						}
					},1000)
				}
			},
			async transferUSDT(){// 转USDT进合约
				if(this.list.USDTAuthorizedAccess < this.list.RemainingAmount){
					return this.authorizedAccess = true
				}
				var code = await this.order.methods.PaymentStateCheck().call({from:this.$address});
				if(code == '5'){					
					this.order.methods.PaymentStateCheck().send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
					})
				}
			},
			updateState(){// 更新状态
				if(this.$BN(this.list.usdt).gte(this.list.totalAmount)){
					this.order.methods.OrderStateCheck().send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
					})
				}
			},
			DoConvertToConsumer(){// 转换状态-获得帮助
				if(this.list.RelatedOrder){
					if(this.list.DT < this.list.ConsumeDT){
						return Toast.fail('DT余额不足');
					}
					// console.log()
					this.order.methods.DoConvertToConsumer().send({
						from : this.$address,
						value: this.$toWei(this.list.ConsumeDT.toString())
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						window.location.reload()
					})
				}else{
					return this.pushProvideHelp = true;
				}
			},
			ApplyProfitingCountDown(){// 申请开始支付倒计时
				this.order.methods.ApplyProfitingCountDown().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			timeDown(endDateStr,currentTime){
				function add(m){return m<10?'0'+m:m }
				//结束时间
				var endDate = endDateStr;
				//当前时间
				var nowDate = currentTime;
				//相差的总秒数
				var totalSeconds = parseInt((endDate - nowDate))
				//天数
				var day=parseInt(totalSeconds/3600/24);
				//取模（余数）
				var modulo = totalSeconds % (60 * 60 * 24);
					
				//小时数
				var hours = Math.floor((modulo / (60 * 60)));
				modulo = modulo % (60 * 60);
				//分钟
				var minutes = Math.floor(modulo / 60);
				//秒
				var seconds = modulo % 60;
				return add(hours+(day*24)) + ":" + add(minutes) + ":" + add(seconds);
			},
			authorizedAccessFun(value){
				this.$contract.getData('usdtTokenI').methods.approve(this.order._address,this.$web3.utils.toWei(value.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , async result=>{
					this.authorizedAccess = false;
					this.authorizationEnds = true;
					this.$set(this.list,'USDTAuthorizedAccess',this.$fromWei(await this.$contract.getData('usdtToken').methods.allowance(this.$address,this.order._address).call({from:this.$address}),'','Mwei'))
					this.transferUSDT()
				})
			},
			end(){
				this.authorizedAccess = false;
			},
		},
		async mounted() {
			this.init(async ()=>{
				var fromBlock = await this.order.methods.blockRange(0).call();
				this.orderWs.events.Log_HelpGet({
					fromBlock: fromBlock
				}, async (error, event)=>{ 
					console.log(event);
				})
				.on('data', async event => {
					var name = await this.$contract.getData('relationship').methods.AddressToNickName(event.returnValues.other).call();
					this.Record.push({
						amount: event.returnValues.amount,
						time: event.returnValues.time,
						address: event.returnValues.other,
						name: this.$web3.utils.hexToUtf8(name),
						type: 'Get',
						order: event.returnValues.order
					})
				})
				.on('changed', function(event){ })
				.on('error', console.error);
				
				this.orderWs.events.Log_HelpTo({
					fromBlock: fromBlock
				}, async (error, event)=>{
					// console.log(event);
				})
				.on('data', async event => {
					var name = await this.$contract.getData('relationship').methods.AddressToNickName(event.returnValues.owner).call();
					this.Record.push({
						amount: event.returnValues.amount,
						time: event.returnValues.time,
						address: event.returnValues.owner,
						name: this.$web3.utils.hexToUtf8(name),
						type: 'To',
						order: event.returnValues.order
					})
				})
				.on('changed', function(event){ })
				.on('error', console.error);				
			})
		},
		destroyed() {
			clearInterval(this.times);
		},
		created(){
			
		}
	}
</script>

