<template>
	<div>
		<div class="van-tabbar-page box settingBox">
			<div class="card" @click="App_Push(bind ? '/upLv' : '/register')">
				<div class="cardLeft">
					<div class="pen" @click.stop="modifyName">
						<span>{{ name }}</span>
						<img src="../../../assets/pen.png" />
					</div>
					<div class="codeAndLv copy"
						@click.stop
						v-clipboard:copy="code"
						v-clipboard:success="onCopySuccess"
						v-clipboard:error="onCopyError"
					>
						<span>{{ code }}</span>
						<img v-lazy="require('@/assets/copy.png')">
					</div>
					<div
					  class="copy"
					  @click.stop
					  v-clipboard:copy="$address"
					  v-clipboard:success="onCopySuccess"
					  v-clipboard:error="onCopyError"
					>
						<span>{{ `${$address.slice(0, 10)}...${$address.slice(-10)}` }}</span>
					</div>
				</div>
				<div class="cardRight">
					<img v-if="origin == '0'" v-lazy="require('@/assets/upLv00.png')">
					<img v-if="origin == '1'" v-lazy="require('@/assets/upLv01.png')">
					<img v-if="origin == '2'" v-lazy="require('@/assets/upLv02.png')">
					<img v-if="origin == '3'" v-lazy="require('@/assets/upLv03.png')">
					<img v-if="origin == '4'" v-lazy="require('@/assets/upLv04.png')">
					<img v-if="origin == '5'" v-lazy="require('@/assets/upLv05.png')">
					<span>{{origin == '0' ? 'VIP' : 'D'+origin}}</span>
				</div>
			</div>
			<div class="content">
				<div @click="InMessage">
					<div :class="message ? 'dot' : ''" class="settingContentLeft">
						<self-icon type="message"></self-icon>
						<span>{{$t('News')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
			</div>
			<div class="content">
				<div @click="bind ? App_Push('/card') : App_Push('/register')">
					<div class="settingContentLeft">
						<self-icon type="link"></self-icon>
						<span>{{$t('invite')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
				<div @click="bind ? App_Push('/friendsList') : App_Push('/register')">
					<div class="settingContentLeft">
						<self-icon type="invite_list"></self-icon>
						<span>{{$t('InvitationList')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
				<div @click="seeActive = !seeActive">
					<div class="settingContentLeft">
						<self-icon type="invite_people"></self-icon>
						<span>{{$t('MyInviter')}}</span>
					</div>
					<div class="settingContentRight">
						<span>{{seeActive ? '******' : GetIntroducerCode}}</span>
						<self-icon type="see" class="see" v-show="!seeActive"></self-icon>
						<self-icon type="seeActive" class="seeActive" v-show="seeActive"></self-icon>
					</div>
				</div>
			</div>
			<div class="content">
				<div @click="bind ? App_Push('/compensate') : App_Push('/register')">
					<div class="settingContentLeft">
						<self-icon type="get_help"></self-icon>
						<span>{{$t('ReceiveDT')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
				<!-- <div @click="App_Push('/exchange')">
					<div class="settingContentLeft">
						<self-icon type="get_help"></self-icon>
						<span>{{$t('swap')}}DT</span>
					</div>
					<self-icon type="next"></self-icon>
				</div> -->
			</div>
			<div class="content">
				<div @click="help">
					<div class="settingContentLeft">
						<self-icon type="help"></self-icon>
						<span>{{$t('help')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
			</div>
			<!-- <div class="content">
				<div @click="bind ? App_Push('/authorizationManagement') : App_Push('/register')">
					<div class="settingContentLeft">
						<self-icon type="Authorize"></self-icon>
						<span>{{$t('USDTAuthorizationManagement')}}</span>
					</div>
					<self-icon type="next"></self-icon>
				</div>
			</div> -->
		</div>
		<overlay :show="show" class="set_name" @click="show = false">
			<div @click.stop>
				<span>{{$t('SetUp')}}</span>
				<input type="text" v-model="nickname" :placeholder="$t('EnterNewNickname')">
				<div>
					<Button @click="show = false">{{$t('cancel')}}</Button>
					<i></i>
					<Button @click="setName" v-ButClickLimit>{{$t('Commit')}}</Button>
				</div>
			</div>
		</overlay>
		
		<overlay :show="upLv" class="upLv" @click="upLv = false">
			<div @click.stop>
				<img v-if="Number(current) == 1" src="../../../assets/upLv1.png" />
				<img v-if="Number(current) == 2" src="../../../assets/upLv2.png" />
				<img v-if="Number(current) == 3" src="../../../assets/upLv3.png" />
				<img v-if="Number(current) == 4" src="../../../assets/upLv4.png" />
				<img v-if="Number(current) == 5" src="../../../assets/upLv5.png" />
				<div>{{$t('Promoted')}}D{{current}}{{$t('level')}}</div>
				<div @click="upLvFun" v-ButClickLimit>{{$t('receive')}}</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Overlay, Button, Toast } from 'vant'
	import selfIcon from '@/components/self-icon.vue'
	
	export default {
		components: { Overlay, Button , selfIcon },
		data() {
			return {
				name: 'DeFi Era',
				code: ' ',
				nickname: '',
				show: false,
				upLv: false,
				message: localStorage.getItem('InMessage') ? JSON.parse(localStorage.getItem('InMessage')) : true,
				bind: false,
				origin: '',
				current: '',
				GetIntroducerCode: '',
				seeActive: false,
			}
		},
		mounted() {
			window.OwnerDistributeAwards = this.OwnerDistributeAwards
			window.OwnerUpdateSwapInfo = this.OwnerUpdateSwapInfo
			this.init()
		},
		methods: {
			init() {
				this.upLv = false;
				this.show = false;
				this.nickname = '';
				
				this.getData()
			},
			getData() {
				if(this.$route.query.invitationCode && this.$route.query.invitationCode.length == 6){localStorage.setItem('invitationCode',this.$route.query.invitationCode)}

				this.$contract.getData('relationship').methods.AddressToShortCode(this.$address)
				.call((err, res) => {
					if (!err) {
						let code = this.$web3.utils.hexToString(res)
						
						this.bind = !(Number(res) === 0)
						this.code = code === '' ? '******' : code

					}
				})
				
				this.$contract.getData('relationship').methods.GetIntroducer(this.$address)
				.call(async (err, res) => {
					if (!err) {
						let GetIntroducerCode = await this.$contract.getData('relationship').methods.AddressToShortCode(res).call()
						if(parseInt(GetIntroducerCode,16)){
							this.GetIntroducerCode = this.$web3.utils.hexToString(GetIntroducerCode)
						}else{
							this.GetIntroducerCode = ''
						}
					}
				})
				
				this.$contract.getData('relationship').methods.AddressToNickName(this.$address)
				.call((err, res) => {
					if (!err) {
						let name = this.$web3.utils.hexToString(res)
						
						this.name = name === '' ? 'DeFi Era' : name
					}
				})
				
				this.$contract.getData('counterManager').methods.UpgradeDLevel()
				.call({from:this.$address},(err, res) => {
					if (!err) {
						// 原等级
						this.origin = res.origin;
						// 可升等级
						this.current = res.current;
						
						if(this.current != this.origin){
							this.upLv = true;
						}
					}
				})
			},
			upLvFun(){
				this.$contract.getData('counterManagerI').methods.UpgradeDLevel().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			async setName() {
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/
				
				if (!reg.test(this.nickname) || this.nickname.length > 8) {
					return Toast.fail(this.$t('warningText13'))
				}
				
				this.$contract.getData('relationshipI').methods.UpdateNickName(this.$web3.utils.stringToHex(this.nickname)).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			onCopySuccess() {
				Toast.success(this.$t('CopySuccessful'))
			},
			onCopyError() {
				Toast.fail(this.$t('CopyFailed'))
			},
			OwnerDistributeAwards(){
				this.$contract.getData('assertPoolAwardsI').methods.OwnerDistributeAwards().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					window.location.reload()
				})
			},
			OwnerUpdateSwapInfo(total,prop){
				this.$contract.getData('swapI').methods.OwnerUpdateSwapInfo(this.$toWei(total.toString()),this.$toWei(prop.toString(),'szabo')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			help(){
				if(this.bind){
					this.App_Push('https://defiera.zendesk.com')
				}else{
					this.App_Push('/register')
				}
			},
			InMessage(){
				if(this.bind){
					localStorage.setItem('InMessage',false)
					this.App_Push('/message')
				}else{
					this.App_Push('/register')
				}
			},
			modifyName(){
				if(this.bind){
					this.show = true
				}else{
					this.show = false
				}
			},
		},
	}
</script>

<style lang="scss" scoped>
	.upLv{
		// padding: ;
		&>div{
			position: relative;
			&>img{
				width: 100%;
			}
			&>div:nth-child(2){
				position: absolute;
				top: 548px;
				color: #FFEA50;
				font-size: 50px;
				font-weight: bold;
				left: 50%;
				transform: translateX(-50%);
			}			
			&>div:nth-child(3){
				position: absolute;
				top: 672px;
				width: 261px;
				height: 83px;
				color: #FAFAFA;
				border: 2px solid #FFEA50;
				font-size: 40px;
				left: 50%;
				transform: translateX(-50%);
				text-align: center;
				line-height: 83px;
				border-radius: 58px;
			}
		}
	}
	.set_name {
		padding: 308px 74px 0;
		&>div {
			width: 100%;
			display: flex;
			align-items: center;
			flex-direction: column;
			padding-top: 28px;
			// background-color: #2A405C;
			// color: #C5D1E1;
			font-size: 40px;
			border-radius: 10px;
			&>input {
				border: none;
				width: calc(100% - 68px);
				padding: 30px;
				margin-top: 46px;
				margin-bottom: 47px;
				border-radius: 15px;
				// background-color: #101720;
			}
			&>div {
				width: 100%;
				display: flex;
				align-items: center;
				border-top: 2px solid #273446;
				&>button {
					border: none;
					width: calc(50% - 1px);
					height: 103px;
					line-height: 103px;
					// color: #C5D1E1;
					font-size: 40px;
					background-color: transparent;
				}
				&>i {
					width: 2px;
					height: 103px;
					background-color: #273446;
				}
			}
		}
	}
	.box {
		padding: 40px 24px 0;
		&>.content {
			width: 100%;
			margin-bottom: 18px;
			padding: 0 21px 0 11px;
			// background-color: #273446;
			border-radius: 8px;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 10px 13px 10px 6px;
				// border-bottom: 1px solid #17212E;
				&>img {
					width: 15px;
					height: 30px;
				}
				&>.settingContentLeft {
					display: flex;
					align-items: center;
					// color: #86A0C2;
					font-size: 30px;
					&>i{
						font-size: 70px;
						margin-right: 22px;
					}
					&>svg{
						font-size: 70px;
						margin-right: 22px;
					}
					&>img {
						width: 80px;
						height: 80px;
						margin-right: 22px;
					}
				}
				&>.settingContentRight {
					display: flex;
					align-items: center;
					// color: #86A0C2;
					font-size: 32px;
					&>img {
						width: 15px;
						height: 30px;
						margin-left: 28px;
					}
					&>.see,&>.seeActive{
						width: 47px;
						height: 31px;
						margin-left: 20px;
					}
				}
				&>.name{
					font-size: 36px;
					color: #596D86;
				}
				&>.dot {
					position: relative;
					&::before {
						content: '';
						width: 20px;
						height: 20px;
						position: absolute;
						top: 10px;
						left: 45px;
						border-radius: 24px;
						background-color: #FF4F29;
					}
				}
				&>svg{
					font-size: 30px;
				}
			}
			&>a:last-child {
				border: none;
			}
		}
		&>.card {
			width: 100%;
			padding: 28px 26px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			// background-color: #273446;
			// box-shadow: 0 6px 4px #000000;
			border-radius: 8px;
			margin-bottom: 42px;
			&>.cardRight{
				display: flex;
				flex-direction: column;
				font-size: 28px;
				// color: #596D86;
				
				&>img {
					width: 67px;
					margin-bottom: 10px;
				}
			}
			&>.cardLeft {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				// color: #86A0C2;
				font-size: 50px;
				&>span:nth-of-type(2) {
					margin-top: 2px;
				}
				&>.codeAndLv{
					display: flex;
					// align-items: center;
					line-height: 1.2;
					font-size: 38px;
					margin: 5px 0;
					
					&>img{
						height: 37px;
						margin-left: 20px;
					}
				}
				&>.copy {
					display: flex;
					align-items: center;
					// color: #516A89;
					font-size: 30px;
					&>img {
						width: 26.04px;
						height: 26px;
						margin-left: 20px;
					}
					&:active {
						opacity: .7;
					}
				}
				&>.pen{
					display: flex;
					align-items: center;
					&>img {
						width: 32px;
						margin-left: 30px;
					}
				}
			}
		}
