import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Register from '../views/register.vue'

import Index from '../views/views/index.vue'
import Setting from '../views/views/setting/setting.vue'
import Exchange from '../views/views/exchange/exchange.vue'

import Card from '../views/views/setting/card.vue'
import ProvideHelp from '../views/views/setting/provideHelp.vue'
import FriendsList from '../views/views/setting/friendsList.vue'
import GetHelp from '../views/views/setting/getHelp.vue'
import ChangeRecord from '../views/views/exchange/changeRecord.vue'
import OrderDetails from '../views/views/setting/orderDetails.vue'
// import MyContract from '../views/views/setting/myContract.vue'
import ShareReward from '../views/views/setting/shareReward.vue'
import Income from '../views/views/income/income.vue'
import IncomeDetail from '../views/views/income/incomeDetail.vue'
import UpLv from '../views/views/setting/upLv.vue'
import Compensate from '../views/views/setting/compensate.vue'
import Order from '../views/views/income/order.vue'
import CompensateRecord from '../views/views/setting/compensateRecord'
import DrawCompensate from '../views/views/drawCompensate'
import End from '../views/views/end'
import LuckPlayer from '../views/views/luckPlayer'
import Message from '../views/views/setting/message.vue'
import MessageCard from '../views/views/setting/messageCard.vue'
import AuthorizationManagement from '../views/views/setting/authorizationManagement.vue'
import CompensationFees from '../views/views/compensationFees.vue'

import Download from '../views/views/download.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'home',
		component: Home
	},
	{
		path: '/register',
		name: 'register',
		component: Register
	},
	{
		path: '/index',
		name: 'index',
		component: Index,
		meta:{
			tabbar: true
		}
	},
	{
		path: '/setting',
		name: 'setting',
		component: Setting,
		meta:{
			tabbar: true
		}
	},
	{
		path: '/exchange',
		name: 'exchange',
		component: Exchange,
		meta:{
			tabbar: true
		}
	},
	{
		path: '/income',
		name: 'income',
		component: Income,
		meta:{
			tabbar: true
		}
	},
	{
		path: '/incomeDetail',
		name: 'incomeDetail',
		component: IncomeDetail
	},
	{
		path: '/card',
		name: 'card',
		component: Card
	},
	{
		path: '/changeRecord',
		name: 'changeRecord',
		component: ChangeRecord
	},
	{
		path: '/provideHelp',
		name: 'provideHelp',
		component: ProvideHelp
	},
	{	path: '/friendsList',
		name: 'friendsList',
		component: FriendsList
	},
	{
		path: '/getHelp',
		name: 'getHelp',
		component: GetHelp
	},
	{
		path: '/orderDetails',
		name: 'orderDetails',
		component: OrderDetails
	},
	// {
	// 	path: '/myContract',
	// 	name: 'myContract',
	// 	component: MyContract
	// },
	{
		path: '/shareReward',
		name: 'shareReward',
		component: ShareReward
	},
	{
		path: '/upLv',
		name: 'upLv',
		component: UpLv
	},
	{
		path: '/compensate',
		name: 'compensate',
		component: Compensate
	},
	{
		path: '/order',
		name: 'order',
		component: Order
	},
	{
		path: '/compensateRecord',
		name: 'compensateRecord',
		component: CompensateRecord
	},
	{
		path: '/drawCompensate',
		name: 'drawCompensate',
		component: DrawCompensate
	},
	{
		path: '/end',
		name: 'end',
		component: End
	},
	{
		path: '/luckPlayer',
		name: 'luckPlayer',
		component: LuckPlayer
	},
	{
		path: '/message',
		name: 'message',
		component: Message
	},
	{
		path: '/messageCard',
		name: 'messageCard',
		component: MessageCard
	},
	{
		path: '/authorizationManagement',
		name: 'authorizationManagement',
		component: AuthorizationManagement
	},
	{
		path: '/compensationFees',
		name: 'compensationFees',
		component: CompensationFees
	},
	{
		path: '/download',
		name: 'download',
		component: Download
	},
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
