<template>
	<div class="box drawCompensateBox colorA1BDE1">
		<img v-if="theme == 'sun'" v-lazy="require('@/assets/logoSun.png')">
		<img v-else v-lazy="require('@/assets/logo.png')">
		<pre>
	{{$t('CompensationDescription')}}
	   {{$t('CompensationDescriptionText')}}
		</pre>
		<div>
			<div>
				<div class="bgColor273446">
					<div>
						<img v-lazy="require('@/assets/draw01.png')">
						<span>{{$t('TotalPH')}}</span>
					</div>
					<span>{{ join }} USDT</span>
				</div>
				<div class="bgColor273446">
					<div>
						<img v-lazy="require('@/assets/draw02.png')">
						<span>{{$t('TotalGH')}}</span>
					</div>
					<span>{{ income }} USDT</span>
				</div>
			</div>
			<div class="bgColor273446">
				<div>
					<img v-lazy="require('@/assets/draw03.png')">
					<span>{{$t('CompensationAmount')}}</span>
				</div>
				<span>{{ amount }} DT</span>
			</div>
		</div>
		<Button color="linear-gradient(45deg, #21C17A, #21C1A7)" @click="draw" v-ButClickLimit>{{$t('receive01')}}</Button>
	</div>
</template>

<script>
	import { Button } from 'vant'
	
	export default {
		components: { Button },
		data() {
			return {
				join: '0',
				income: '0',
				amount: '0',
				theme: localStorage.getItem('theme')
			}
		},
		mounted() {
			this.init()
		},
		methods: {
			init() {
				if (this.$cache.drawCompensate) {
					this.join = this.$cache.drawCompensate.join
					this.income = this.$cache.drawCompensate.income
					this.amount = this.$cache.drawCompensate.amount
				} else {
					this.$cache.drawCompensate = {}
				}
				
				this.getData()
			},
			getData() {
				Promise.all([
					this.$contract.getData('phoenixState').methods.dataStateVersion().call({ from: this.$address }),
					this.$contract.getData('phoenixState').methods.SettlementCompensate().call({ from: this.$address })
				]).then(res => {
					// console.log(res)
					if (res[1] == 0) this.$router.replace('/end')
					this.amount = this.$numFixed(this.$fromWei(res[1]), 2)
					this.$cache.drawCompensate.amount = this.amount
					
					this.$contract.getData('phoenixState').methods.inoutMapping(Number(res[0]) > 0 ? Number(res[0]) - 1 : res[0], this.$address)
					.call({ from: this.$address }, (err, result) => {
						if (!err) {
							this.join = this.$numFixed(this.$fromWei(result.totalIn, '', 'mwei'), 2)
							this.income = this.$numFixed(this.$fromWei(result.totalOut, '', 'mwei'), 2)
							
							this.$cache.drawCompensate.join = this.join
							this.$cache.drawCompensate.income = this.income
						}
					})
				})
			},
			draw() {
				this.$contract.getData('phoenixI').methods.SettlementCompensate()
				.send({ from : this.$address })
				.on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
					this.App_Push('/end')
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		// color: #A1BDE1;
		padding: 58px 38px 0;
		&>img {
			width: 152px;
			height: 188px;
			margin-bottom: 52px;
		}
		&>pre {
			width: 100%;
			height: 180px;
			font-size: 28px;
			text-align: left;
			line-height: 40px;
			padding: 30px 24px;
			white-space: pre-wrap;
			border: 2px solid #21C17A;
			border-radius: 15px;
			// background-color: #273446;
		}
		&>div {
			width: 100%;
			display: flex;
			align-items: center;
			flex-direction: column;
			margin: 40px 0 92px;
			&>div {
				width: 100%;
				&:first-child {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 40px;
					&>div {
						font-size: 40px;
						width: calc(50% - 17px);
						padding: 34px 0 24px;
						border-radius: 10px;
						// background-color: #273446;
						&>div {
							display: flex;
							align-items: center;
							justify-content: center;
							font-size: 32px;
							margin-bottom: 16px;
							&>img {
								margin-right: 44px;
							}
						}
						&:first-child>div>img {
							width: 50px;
							height: 50px;
						}
						&:last-child>div>img {
							width: 48px;
							height: 50px;
						}
					}
				}
				&:last-child {
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 40px;
					border-radius: 10px;
					// background-color: #273446;
					padding: 38px 0 16px;
					&>div {
						font-size: 32px;
						display: flex;
						align-items: center;
						flex-direction: column;
						margin-right: 136px;
						&>img {
							width: 43px;
							height: 50px;
							margin-bottom: 24px;
						}
					}
				}
			}
		}
		&>button {
			width: 400px;
			height: 96px;
			font-size: 35px;
			line-height: 96px;
			border-radius: 50px;
		}
	}
</style>
