<template>
	<div class="compenstate box">
		<!-- <nav-bar :title="title" rightIcon="record" :rightClick="pushCompensateRecord" ></nav-bar> -->
		<div class="navBox compenstateBox">
			<div class="compenstateCircle">
				<div>{{SettlementCompensate ? SettlementCompensate : currentAmout}}</div>
				<span>{{$t('Available02')}}/DT</span>
			</div>
			<div class="compenstateInfo colorA1BDE1">
				<div>
					<img src="../../../assets/getReward.png" />
					<span>{{$t('Compensation')}}</span>
					<span>{{total}} DT</span>
				</div>
				<p></p>
				<div>
					<img src="../../../assets/wait.png" />
					<span>{{$t('Locked')}}</span>
					<span>{{amout}} DT</span>
				</div>
			</div>
			<Button @click="send" v-ButClickLimit>{{SettlementCompensate ? $t('Release') : $t('receive')}}</Button>
		</div>
	</div>
</template>

<script>
	import { Button , Toast } from 'vant'
	import navBar from '@/components/nav-bar'
	
	export default{
		components:{ navBar , Button },
		data(){
			return {
				title: this.$t('ReceiveDT'),
				SettlementCompensate: 0,
				total: 0,
				amout: 0,
				currentAmout: 0,
			}
		},
		methods:{
			async init(){
				this.$contract.getData('phoenix').methods.SettlementCompensate().call({
					from:this.$address,
				},async (err,result)=>{
					this.SettlementCompensate = this.$fromWei(result)
					if(!this.SettlementCompensate){
						var compensateMapping = await this.$contract.getData('phoenixState').methods.compensateMapping(this.$address).call()
						this.total = this.$fromWei(compensateMapping.total)
						this.amout = this.$fromWei(this.$BN(compensateMapping.total).sub(this.$BN(compensateMapping.currentWithdraw)).toString())
						this.currentAmout = this.$fromWei(await this.$contract.getData('phoenix').methods.WithdrawCurrentRelaseCompensate().call({from:this.$address}))
					}
				})
				// console.log(await this.$contract.getData('phoenix').methods.SettlementCompensate().call({from:this.$address}))
				// console.log(await this.$contract.getData('phoenixState').methods.inoutMapping(0,this.$address).call())
			},
			pushCompensateRecord(){
				this.App_Push('/compensateRecord')
			},
			send(){
				if(this.SettlementCompensate){
					this.$contract.getData('phoenixI').methods.SettlementCompensate().send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
					})
				}else{
					if(!this.currentAmout){return Toast.fail(this.$t('NoDTAvailable'))}
					this.$contract.getData('phoenixI').methods.WithdrawCurrentRelaseCompensate().send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
					})
				}
			},
		},
		mounted() {
			this.init()
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>
<style>
	.compenstate i{
		font-size: 45px!important;
		/* color: #A1BDE1; */
	}
</style>
<style lang="scss" scoped>
	.compenstateBox{
		padding: 50px 24px 0;
		
		.compenstateCircle{
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 28px;
			// color: #FFFFFF;
			
			&>div{				
				width: 240px;
				height: 240px;
				border-radius: 50%;
				background: linear-gradient(180deg,#21C1A8 0%,#21C17A 100%);
				font-size: 60px;
				margin-bottom: 28px;
				line-height: 240px;
			}
		}
		
		.compenstateInfo{
			width: 100%;
			height: 272px;
			border-radius: 20px;
			// background-color: #273446;
			display: flex;
			justify-content: space-between;
			margin: 70px 0 106px;
			// color: #A1BDE1;
		
			// &>p{
			// 	border-right: 2px solid #18222F;
			// }
			
			&>div{
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				
				&>img{
					display: block;
					// width: auto;
					height: 60px;
				}
				
				&>span:nth-child(2){
					font-size: 32px;
					margin: 20px 0 15px;
				}
				
				&>span:nth-child(3){
					font-size: 40px;
				}
			}
		}
		
		&>button{
			width: 400px;
			height: 96px;
			background: linear-gradient(270deg,#21C1A8 0%,#21C17A 100%);
			border: none;
			border-radius: 50px;
			font-size: 40px;
			color: #fff;
		}
	}
</style>
