<template>
	<div class="box friendsListBox">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="header">
			<div
			  v-for="(item, index) in history"
			  :key="index"
			  @click="viewHistory(index)"
			>{{ item.name }}</div>
		</div>
		<div class="content">
			<div
			  v-for="(item, index) in list"
			  :key="index"
			  @click="addHistory(item.name, item.address)"
			>
				<div>
					<div v-if="item.grade > 0">
						<span>{{ item.name }}</span>
						<img v-lazy="require(`@/assets/lv${item.grade}.png`)">
						<span>(D{{ item.grade }})</span>
					</div>
					<div v-else>
						<span>{{ item.name }}</span>
						<img v-lazy="require('@/assets/upLv00.png')">
					</div>
					<span>{{ item.code }}</span>
				</div>
				<div>
					<span>{{ item.amount }} <span>USDT</span></span>
					<img v-lazy="require('@/assets/next.png')">
				</div>
			</div>
			<not-record v-if="!list.length" :text="NoFriendsRecord"></not-record>
		</div>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import notRecord from '@/components/not-record'
	
	export default {
		components: { navBar, notRecord },
		data() {
			return {
				title: this.$t('FriendsList'),
				list: [
					// { name: '001', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '002', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '003', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '004', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '005', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '006', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '007', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '008', code: '123456', grade: 1, amount: '0.12', address: '123' },
					// { name: '009', code: '123456', grade: 1, amount: '0.12', address: '123' },
				],
				history: [],
				NoFriendsRecord: this.$t('NoFriendsRecord')
			}
		},
		mounted() {
			this.init()
		},
		methods: {
			init() {
				if (this.$cache.friendsList) {
					this.list = this.$cache.friendsList.list
					this.history = this.$cache.friendsList.history
				} else {
					this.$cache.friendsList = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.getData('relationship').methods.AddressToNickName(this.$address)
				.call((err, res) => {
					if (!err) {
						let history = [{
							name: this.$web3.utils.hexToString(res),
							address: this.$address
						}]
						
						this.history = history
						this.$cache.friendsList.history = history
					}
				})
				
				this.getList(this.$address)
			},
			getList(address) {
				this.$contract.getData('relationship').methods.RecommendList(address)
				.call((err, res) => {
					if (!err) {
						let list = []
						
						res.list.forEach(async item => {
							let name = await this.$contract.getData('relationship').methods.AddressToNickName(item).call(),
								code = await this.$contract.getData('relationship').methods.AddressToShortCode(item).call(),
								info = await this.$contract.getData('counterManager').methods.InfomationOf(item).call()
								
							list.push({
								name: this.$web3.utils.hexToString(name),
								code: this.$web3.utils.hexToString(code),
								grade: Number(info.dlevel),
								amount: this.$numFixed(this.$fromWei(info.selfAchievements, '', 'Mwei'), 2),
								address: item
							})
						})
						
						this.list = list
						if (address === this.$address) {
							this.$cache.friendsList.list = list
						}
					}
				})
			},
			viewHistory(index) {
				this.getList(this.history[index].address)
				this.history = this.history.slice(0, index + 1)
			},
			addHistory(name, address) {
				this.getList(address)
				this.history.push({ name, address })
				setTimeout(() => {
					document.querySelector('.header>div:last-child').scrollIntoView({
						behavior: 'smooth'
					})
				})
			}
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		padding-top: 40px;
	}
	.content {
		width: 100vw;
		height: calc(100vh - 200px);
		overflow-y: auto;
		position: relative;
		padding: 20px 0 0 30px;
		background-color: #273446;
		&>div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-right: 40px;
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 2px solid #516A89;
			&>div {
				display: flex;
				&:first-child {
					color: #7489A5;
					font-size: 28px;
					flex-direction: column;
					align-items: flex-start;
					&>div {
						display: flex;
						align-items: center;
						margin-bottom: 20px;
						&>span {
							&:first-child {
								color: #8FACD2;
								font-size: 32px;
							}
							&:last-child {
								color: #A1BDE1;
							}
						}
						&>img {
							width: 38px;
							height: 38px;
							margin: 0 19px;
						}
					}
				}
				&:last-child {
					align-items: center;
					&>span {
						color: #8FACD2;
						font-size: 32px;
						&>span {
							font-size: 20px;
							margin-right: 18px;
						}
					}
					&>img {
						width: 15px;
						height: 30px;
					}
				}
			}
		}
		&>.not_record {
			margin: 0;
			padding: 0;
			border: none;
		}
	}
	.header {
		width: 100vw;
		height: 60px;
		font-size: 0;
		text-align: left;
		line-height: 60px;
		white-space: nowrap;
		margin-bottom: 20px;
		overflow: auto hidden;
		&::-webkit-scrollbar {
			display: none;
		}
		&>div {
			display: inline-block;
			width: 170px;
			height: 60px;
			color: #A1BDE1;
			font-size: 28px;
			margin-right: 5px;
			text-align: center;
			position: relative;
			// background-color: #31435C;
			&::before, &::after {
				content: '';
				width: 42px;
				height: 42px;
				position: absolute;
				top: 8px;
				transform: rotate(45deg);
			}
			&::before {
				left: -21px;
				background-color: #18222F;
			}
			&::after {
				z-index: 1;
				right: -21px;
				// background-color: #31435C;
			}
			// &:active {
			// 	background-color: rgba(#31435C, .7);
			// 	&::after {
			// 		background-color: rgba(#31435C, .7);
			// 	}
			// }
			&:first-child::before {
				display: none;
			}
		}
	}
</style>
