<template>
	<popup v-model="showFun" @click-overlay='clickOverlay' position="bottom">
		<div class="authorizedAccessBox">
			<!-- <div class="authorizedAccessTop">
				<div><img src="../assets/yellowRing.png"/></div>
				<p>{{$t('Participation')}}</p>
			</div> -->
			<div class="authorizedAccessTop">
				<div><img src="../assets/yellowKey.png"/></div>
				<p>{{$t('ContractAuthorization')}}</p>
				<!-- <span>{{authorizedAccessTime}}</span> -->
			</div>
			<div class="authorizedAccessText">{{$t('ContractAuthorizationText01')}}</div>
			<div class="authorizedAccessInput">
				<p>{{$t('ContractAuthorizationText02')}}</p>
				<div>
					<!-- <input type="number" v-model="authorizedAccessNumber" @input="authorizedAccessInput" placeholder="授权额度" disabled /> -->
					<div>{{authorizedAccessNumber}} {{tokenTypeFun}}</div>
				</div>
				<!-- <span>{{$t('quotaEmpowerText3')}} {{availableAuthorizationsFun}} {{tokenTypeFun}}</span> -->
			</div>
			<div class="authorizedAccessButton" @click="click(authorizedAccessNumber)" v-ButClickLimit>{{$t('Commit')}}</div>
		</div>
	</popup>
</template>

<script>
	import { Popup } from 'vant';
	export default {
		components:{ Popup },
		data () {
			return {
				availableAuthorizationsFun: this.availableAuthorizations,//暂时无用
				showFun: this.show,//显示
				needAuthorizationFun: this.needAuthorization,//数量
				// authorizedAccessTime: '',//时间
				authorizedAccessNumber: '',//输入框的数量
				tokenTypeFun: this.tokenType,//token类型
			}
		},
		props:{
			show:{
				type: Boolean,
				default: false
			},
			click:{
				type: Function
			},
			needAuthorization: {},
			availableAuthorizations: {},
			tokenType: {
				type: String
			},
			authorizationEnds:{
				type: Boolean,
				default: false
			},
		},
		watch:{
			show(){
				this.showFun = this.show;
				// this.authorizedAccessTime = this.$toTime(new Date().getTime()/1000,'Y/M/D H:i')
				this.needAuthorizationFun = this.needAuthorization
				this.authorizedAccessNumber = this.needAuthorization
			},
			needAuthorization(){
				this.needAuthorizationFun = this.needAuthorization
				this.authorizedAccessNumber = this.needAuthorization
			},
			availableAuthorizations(){
				this.availableAuthorizationsFun = this.availableAuthorizations
			},
			authorizationEnds(){
				if(this.authorizationEnds){
					this.authorizedAccessNumber = '';
				}
			},
			tokenType(){
				this.tokenTypeFun = this.tokenType
			}
		},
		methods:{
			authorizedAccessInput(){
				this.authorizationEnds = false;
			},
			clickOverlay(){
				this.authorizedAccessNumber = ''
				this.$emit('authorizationEnds','end')
			}
		}
	}
</script>

<style scoped lang="scss">
	.van-popup{
		background: transparent;
	}
	.authorizedAccessBox{
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 60px 40px;
		// background: #2A405C;
		border-radius: 50px 50px 0 0;
		
		&>div{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		
		.authorizedAccessTop{
			width: 100%;
			font-size: 32px;
			// border-bottom:2px solid rgba(6,33,54,1);
			// color: #A1BDE1;
			padding-bottom: 28px;
			margin-bottom: 46px;
			
			&>div{
				width: 106px;
				height: 106px;
				border-radius: 50%;
				// border: 5px solid #FFCE00;
				margin-bottom: 30px;
				
				img{
					width: 100px;
					height: 112px;
				}
			}
			
			&>span{
				font-size: 24px;
			}
		}
		
		.authorizedAccessText{
			position: relative;
			font-size: 28px;
			// color: #A1BDE1;
			margin-bottom: 46px;
			text-align: left;
			padding-left: 10px;
		}
		
		.authorizedAccessText:after{
			position: absolute;
			content: '';
			width: 6px;
			height: 32px;
			// background-color: rgba(58,156,255,1);
			left: -15px;
			top: 3px;
		}
		
		.authorizedAccessInput{
			width: 100%;
			font-size: 26px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 56px;
			
			&>div{
				display: flex;
				font-size: 36px;
				width: 100%;
				// height: 110px;
				// border: 2px solid #516a89;
				border-radius: 10px;
				align-items: center;
				margin: 18px 0 14px;
				// background-color: #303e52;
				color: #21C19E;
				padding: 10px 20px;
				
				// &>div{
				// 	display: flex;
				// 	align-items: center;
				// 	justify-content: center;
				// 	width: 194px;
				// 	height: 100%;
				// 	font-size: 40px;
				// 	border-left: 2px solid #3A9CFF;
				// }
				
				// &>input{
				// 	flex: 1;
				// 	width: 100%;
				// 	height: 100%;
				// 	background-color: transparent;
				// 	border: none;
				// 	padding-left: 38px;
				// }
				
			}
			
			&>p{
				// color: #879FC0;
				font-size: 36px;
				padding-left: 12px;
			}
			
			&>span{
				color: #A1BDE1;
			}
		}
		
		.authorizedAccessButton{
			width: 390px;
			height: 80px;
			background: #21C19E;
			border-radius: 50px;
			font-size: 32px;
			color: #fff;
		}
	}
</style>
