<template>
	<div class="box">
		<!-- <nav-bar :title="title"></nav-bar> -->
		<div class="navBox">
			<div>
				<div class="card">
					<div>
						<span>{{$t('IncomeBalance')}}</span>
						<span>{{Amount}} <span>USDT</span></span>
					</div>
					<div>
						<span>{{$t('ReceivableIncome')}}</span>
						<span>{{rewardAmount}} <span>USDT</span></span>
					</div>
				</div>
				<div class="title">
					<span>{{$t('GetHelp01')}}</span>
					<!-- <span @click="ExtractAll">{{$t('ExtractAll')}}</span> -->
				</div>
				<div class="input">
					<div>
						<input type="number" :placeholder="$t('PleaseEnterMultipleOf10')" v-model="value" @input="input">
					</div>
					<span>USDT</span>
				</div>
				<!-- <div class="input">
					<input type="number" placeholder="消耗燃料" v-model="ConsumeDT" disabled>
					<span>DT</span>
				</div> -->
				<div class="Underline ConsumeDT">
					<span>{{$t('ExtractAll')}}</span>
					<span>- {{FuelConsumption}} DT</span>
				</div>
				<div class="AvailableDT">
					<span>{{$t('Available')}}：</span>
					<span>{{DT}} DT</span>
				</div>
				<!-- <span>可用 {{DT}} DT</span> -->
				<Button color="linear-gradient(44deg, #21C17A, #21C1A8)" @click="createOrder" v-ButClickLimit>{{$t('Commit')}}</Button>
				<div class="checkbox" @click="rule = true">
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
			</div>
		</div>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div>
				<div class="ruleTop">					
					<div>
						<icon name="fail"></icon>
					</div>
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
				<div class="ruleBottom">
					<p>1、{{$t('ReceivingInstructionsText01')}}；</p>
					<p>2、{{$t('ReceivingInstructionsText02')}}；</p>
					<p>3、{{$t('ReceivingInstructionsText03')}}；</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Button, Icon , Toast , Overlay } from 'vant'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { navBar, Button, Icon , Overlay },
		data() {
			return {
				title: this.$t('PromotionRewards'),
				rewardAmount: 0,
				DT: 0,
				totalRewardedAmount: 0,
				teamMemberCount: 0,
				Amount: 0,
				value: '',
				WithdrawCostProp: 0,
				USDTToDTProp: 0,
				ConsumeDT: 0,
				rule: false,
			}
		},
		methods:{
			async init(){
				// this.DT = this.$fromWei(await this.$contract.getData('referenceToken').methods.balanceOf(this.$address).call())
				this.IWeb3.eth.getBalance(this.$address).then(result=>{
					this.DT = this.$fromWei(result)
				})
				
				// 可提取/累计收益
				var RewardInfo = await this.$contract.getData('reward').methods.RewardInfo(this.$address).call()
				this.rewardAmount = this.$fromWei(RewardInfo.rewardAmount,'','Mwei')
				this.totalRewardedAmount = this.$fromWei(this.$BN(RewardInfo.rewardAmount).add(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')
				// 人数
				var InfomationOf = await this.$contract.getData('counterManager').methods.InfomationOf(this.$address).call()
				this.teamMemberCount = InfomationOf.teamMemberCount
				
				if(this.rewardAmount < this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')){
					this.Amount = this.rewardAmount
				}else{
					this.Amount = this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')
				}
				
				// 消耗燃料公式
				this.WithdrawCostProp = await this.$contract.getData('config').methods.WithdrawCostProp().call();
				this.USDTToDTProp = await this.$contract.getData('config').methods.USDTToDTProp().call();
				
				// 最低可提取收益
				this.OrderAmountGranularity = this.$fromWei(await this.$contract.getData('config').methods.OrderAmountGranularity().call(),'','Mwei');
				// this.ConsumeDT = this.$fromWei(this.$BN(this.$toWei((CurrentP1 + CurrentP2).toString(),'Mwei')).mul(this.$BN(WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(USDTToDTProp)).toString()));
			},
			input(){
				var value = Number(this.value);
				if(value == '' ||  value == 0){
					this.value = ''
					this.ConsumeDT = ''
					return 
				}else{
					this.ConsumeDT = this.$fromWei(this.$BN(this.$toWei((value).toString(),'Mwei')).mul(this.$BN(this.WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(this.USDTToDTProp)).toString())
				}
			},
			async createOrder(){
				if(!this.rewardAmount){
					return Toast.fail(this.$t('warningText08')+'0');
				}else if(this.rewardAmount < this.OrderAmountGranularity){
					return Toast.fail(this.$t('warningText09')+this.OrderAmountGranularity);
				}else if(this.value > this.Amount){
					return Toast.fail(this.$t('warningText10'));
				}else if(this.value % 10 != 0){
					return Toast.fail(this.$t('PleaseEnterMultipleOf10'));
				}else if(this.ConsumeDT > this.DT){
					return Toast.fail(this.$t('warningText11'));
				}
				var state = await this.$contract.getData('controller').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).call({from: this.$address})
				// console.log(state)
				if(state == '0'){					
					this.$contract.getData('controllerI').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).send({
						from: this.$address,
						value: this.ConsumeDT
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					})
					.on('receipt' , result=>{
						this.App_Push('/order')
					})
				}else if(state == 6){
					Toast.fail(this.$t('warningText12'))
				}
			},
			// ExtractAll(){
			// 	this.value = this.Amount;
			// 	var value = this.value;
			// 	this.ConsumeDT = this.$fromWei(this.$BN(this.$toWei((value).toString(),'Mwei')).mul(this.$BN(this.WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(this.USDTToDTProp)).toString())
			// }
		},
		mounted() {
			this.init()
		},
		created(){
			if(window.xwNavigatorSetTitle){
				xwNavigatorSetTitle.postMessage(this.title);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		padding: 0 28px;
		&>div {
			display: flex;
			flex-direction: column;
			width: 100%;
			border-radius: 10px;
			padding: 36px 42px 60px;
			background-color: #273446;
			box-shadow: 0 6px 4px #000000;
			&>.checkbox {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-top: 40px;
				&>span {
					font-size: 22px;
					text-decoration: underline;
				}
			}
			&>button {
				width: 400px;
				height: 96px;
				font-size: 40px;
				line-height: 96px;
				margin: 62px auto 0;
				border-radius: 10px;
			}
			&>span {
				color: #A1BDE1;
				font-size: 30px;
				text-align: left;
			}
			&>.input {
				width: 100%;
				height: 112px;
				display: flex;
				align-items: center;
				border-radius: 10px;
				font-size: 36px;
				border: 1px solid #516A89;
				background-color: #101720;
				&:nth-of-type(2) {
					margin-bottom: 30px;
				}
				&:nth-of-type(3) {
					margin-bottom: 16px;
				}
				&>div, &>input {
					width: 432px;
					height: 100%;
				}
				&>div {
					display: flex;
					align-items: center;
					&>input {
						width: 100%;
						height: 100%;
						border: none;
						padding: 0 42px;
						font-size: 26px;
						background-color: transparent;
					}
					&>div {
						height: 100%;
						display: flex;
						align-items: center;
						flex-direction: column;
						justify-content: space-between;
						padding: 30px 20px 30px 0;
						&>img {
							width: 20px;
							height: 10px;
							&:first-child {
								transform: rotate(180deg);
							}
						}
					}
				}
				&>input {
					border: none;
					padding: 0 42px;
					font-size: 26px;
					background-color: transparent;
				}
				&>span {
					width: calc(100% - 432px);
					line-height: 112px;
					border-left: 1px solid #516A89;
				}
			}
			&>.ConsumeDT{
				&>span:nth-child(2){
					font-size: 40px;
					color: #EB4948;
				}
			}
			&>.Underline{
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding-bottom: 34px;
				border-bottom: 1px solid #516A89;
				font-size: 30px;
				color: #A1BDE1;
				margin-top: 42px;
			}
			&>.AvailableDT{
				font-size: 24px;
				text-align: right;
				color: #A1BDE1;
				margin-top: 16px;
			}
			&>.title{
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 30px;
				color: #A1BDE1;
				margin-bottom: 20px;
				&>span:nth-child(2){
					color: #1FA36E;
					font-size: 24px;
					text-decoration: underline;
				}
			}
			&>.card {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 36px;
				&>div {
					width: calc(50% - 23px);
					color: #A1BDE1;
					font-size: 26px;
					display: flex;
					border-radius: 10px;
					flex-direction: column;
					align-items: flex-start;
					padding: 16px 0 16px 14px;
					background-color: #2A405C;
					border: 2px solid #516A89;
					&>span:nth-of-type(2) {
						font-size: 32px;
						margin-top: 6px;
						margin-bottom: 5px;
						&>span {
							font-size: 20px;
						}
					}
				}
			}
		}
	}
	
	.rule {
		z-index: 2!important;
		padding: 0 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		
		&>div{
			width: 100%;
			padding: 54px 0 250px;
			background-color: #273446;
			color: #A1BDE1;
			border-radius: 10px;
			
			.ruleTop{
				border-bottom: 2px solid #516A89;
				padding-bottom: 24px;
				font-size: 32px;
				
				&>div{
					width:91px;
					height:91px;
					background:rgba(33,193,141,1);
					border-radius: 50%;
					color: #fff;
					font-size: 80px;
					display: flex;
					justify-content: center;
					align-items: center;
					margin: 0 auto 20px;
				}
			}
			
			.ruleBottom{
				text-align: left;
				padding: 72px 30px 0 58px;
				font-size: 28px;
				position: relative;
				
				&>div{
					position: absolute;
					width: 6px;
					height: 32px;
					background-color: #258879;
