<template>
	<div id="box">
		<div class="imtoken" v-if="weixin || imtoken">
			请点击右上角，使用浏览器打开
			<img src="@/assets/line.png" />
		</div>
		<div class="box">
			<img src="@/assets/downloadLogo.png" />
			<span class="name" v-if="code != '******'">{{name}}</span>
			<div class="text">邀请你加入<span>DeFi</span><span> Era</span></div>
			<span class="code" v-if="code != '******'">{{code}}</span>
			<span class="codeText" v-if="code != '******'">您的专属邀请码</span>
			<button class="downloadButton" @click="download">立即下载</button>
		</div>
	</div>
</template>

<script>
export default {
	name: 'app',
	data(){
		return {
			name: 'DeFiEra',
			code: '******',
			appDownloadAddress: 'https://dtttesttt.oss-cn-shenzhen.aliyuncs.com/app/dfpro_latest.apk',
			imtoken: false,
			weixin: false,
		}
	},
	methods:{
		download(){
			if(!this.code){
				let link = document.createElement('a')
				link.href = this.appDownloadAddress
				link.click()
				return
			}
			this.$copyText(this.code).then(result=>{
				let link = document.createElement('a')
				link.href = this.appDownloadAddress
				link.click()
			},err=>{
				console.log(err)
			})
		},
	},
	mounted() {
		try{			
			//钱包打开，提示浏览器打开
			if(!window.ethereum.host == 'http://127.0.0.1:4321'){
				this.imtoken = true;
			}
		}catch(e){}
		
		//判断是否是微信
		var ua = navigator.userAgent.toLowerCase();
		if(ua.match(/MicroMessenger/i) == "micromessenger") {
			this.weixin = true;
		}
		
		//获取邀请码和名字
		let code = this.$route.query.code;
		let name = this.$route.query.name;
		
		if(code && name){
			this.code = code
			this.name = name
		}
	}
}
</script>

<style lang="scss" scoped>
#box {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	background-color: #273446;
	width: 100vw;
	height: 100vh;
	overflow: auto;
	// display: flex;
	// align-items: center;
	// justify-content: center;
}
.imtoken{
	background-color: #10242d;
	color: #b9b9b9;
	font-size: 30px;
	height: 150px;
	display: flex;
	align-items: center;
	padding-left: 100px;
	position: relative;
	
	&>img{
		width: 90px;
		position: absolute;
		right: 70px;
		top: 30px;
	}
}
.box{
	color: #8FACD2;
	padding: 212px 0 100px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: 50px;
	
	img{
		width: 160px;
		height: 178px;
		margin-bottom: 20px;
	}
	
	.text{
		margin: 20px 0;
		
		&>span:nth-child(1){
			color: #21C18C;
		}
		
		&>span:nth-child(2){
			color: #fff;
		}
	}
	
	.code{
		font-size: 90px;
		color: #21C18C;
		margin-bottom: 20px;
	}
	
	.codeText{
		font-size: 30px;
	}
	
	.downloadButton{
		width: 400px;
		height: 96px;
		font-size: 40px;
		border-radius: 50px;
		border: none;
		background: linear-gradient(45deg,#21C17A 0%,#21C1A7 100%);
		color: #fff;
		margin: 200px 0 80px;
	}
	
	.openApp{
		color: #fff;
		font-size: 40px;
	}
}
</style>
